import { InteractionBase } from "./interaction-base";
import { PluginInteractionConfig } from "../../interfaces";

export class ContextMenu extends InteractionBase {
  onCreate(id: string, interaction: PluginInteractionConfig) {
    this.registerListener(this.context.pluginComponentRef.location.nativeElement, "contextmenu", (e) => {
      if (interaction.config?.preventDefault) {
        e.preventDefault();
      }
      this.fire().subscribe();
    });
  }
  onUpdate() {}
  onDestroy() {}
}
