import { ElementButton } from "./../../../group.interface";
export const processButtons = (elementButtons: { [id: string]: ElementButton }) => {
  const buttons = [];
  if (!elementButtons) {
    return buttons;
  }
  Object.keys(elementButtons)
    .sort((a, b) => {
      const aEl = elementButtons[a];
      const bEl = elementButtons[b];
      if (aEl.index > bEl.index) {
        return 1;
      }
      if (aEl.index < bEl.index) {
        return -1;
      }
      return 0;
    })
    .forEach((id) => {
      buttons.push(elementButtons[id]);
    });
  return buttons;
};
