import { Component, OnInit, Renderer2, OnDestroy, ElementRef, ChangeDetectorRef, ViewChild, HostListener } from "@angular/core";
import { Subject } from "rxjs";
import { ScaleData, DragData, ElementButton } from "../../group.interface";

@Component({
  selector: "app-layout-element-controls",
  templateUrl: "./layout-element-controls.component.html",
  styleUrls: ["./layout-element-controls.component.scss"],
})
export class LayoutElementControlsComponent implements OnInit, OnDestroy {
  @ViewChild("buttonsRef", { read: ElementRef, static: false }) buttons: ElementRef;

  public ids: string[];
  public wrapperEl: ElementRef;
  info: string;
  elementButtons: { [id: string]: ElementButton };

  dragStart$: Subject<{ ids: string[]; data: DragData; elementButton?: ElementButton; e: MouseEvent }> = new Subject();
  drag$: Subject<{ ids: string[]; data: DragData; elementButton?: ElementButton; e: MouseEvent }> = new Subject();
  dragEnd$: Subject<{ ids: string[]; data: DragData; elementButton?: ElementButton; e: MouseEvent }> = new Subject();
  scale$: Subject<{ ids: string[]; data: ScaleData; elementButton?: ElementButton; e: MouseEvent }> = new Subject();
  scaleEnd$: Subject<{ ids: string[]; data: ScaleData; elementButton?: ElementButton; e: MouseEvent }> = new Subject();
  scaleStart$: Subject<{ ids: string[]; data: ScaleData; elementButton?: ElementButton; e: MouseEvent }> = new Subject();
  mouseOver$: Subject<{ ids: string[]; data: any; elementButton?: ElementButton; e: MouseEvent }> = new Subject();
  mouseLeave$: Subject<{ ids: string[]; data: any; elementButton?: ElementButton; e: MouseEvent }> = new Subject();

  click$: Subject<{ ids: string[]; data: any; elementButton?: ElementButton; e: MouseEvent }> = new Subject();
  buttonEvent$: Subject<{ ids: string[]; data: any; elementButton?: ElementButton; e: MouseEvent }> = new Subject();
  doubleClick$: Subject<{ ids: string[]; data: any; elementButton?: ElementButton; e: MouseEvent }> = new Subject();

  mouseMoveListener;
  mouseUpListener;

  dragActive = false;
  scaleActive = false;

  dragData: DragData | ScaleData = {
    startX: 0,
    startY: 0,
    deltaX: 0,
    deltaY: 0,
  };
  elementButton;

  left;
  top;
  width;
  height;
  @HostListener("click", ["$event"]) evt(e) {
    this.click$.next({ ids: this.ids, data: null, e: e });
  }
  constructor(private renderer: Renderer2, private cd: ChangeDetectorRef) {}
  handleButtonEvent(e: { event: MouseEvent; elementButton: ElementButton }) {
    if (e.elementButton.supportDrag) {
      this.elementButton = e.elementButton;
      this.dragStart(e.event, this.elementButton);
    } else {
      this.buttonEvent$.next({ ids: this.ids, data: null, elementButton: e.elementButton, e: e.event });
    }
  }
  update(left, top, width, height) {
    this.left = left;
    this.top = top;
    this.width = width;
    this.height = height;
  }
  ngOnInit(): void {}

  doubleClick(e) {
    this.doubleClick$.next({ ids: this.ids, data: null, e: e });
  }
  mouseOver(e) {
    this.mouseOver$.next({ ids: this.ids, data: e, e: e });
  }
  mouseLeave(e) {
    this.mouseLeave$.next({ ids: this.ids, data: e, e: e });
  }
  setIds(ids: string[]) {
    this.ids = ids;
  }
  dragStart(e, elementButton: ElementButton = null) {
    // this.click$.next({ ids: this.ids, data: null, e: e });

    e.preventDefault();
    this.dragActive = true;
    const rect = this.wrapperEl.nativeElement.getBoundingClientRect();
    this.dragData.startX = e.pageX - rect.left;
    this.dragData.startY = e.pageY - rect.top;
    delete this.dragData["direction"];
    this.registerMouseListeners();
    this.dragStart$.next({ ids: this.ids, data: this.dragData as DragData, elementButton, e: e });
  }
  scaleStart(e, direction: "n" | "s" | "w" | "e" | "nw" | "ne" | "sw" | "se") {
    e.preventDefault();

    const rect = this.wrapperEl.nativeElement.getBoundingClientRect();
    this.dragData.startX = e.pageX - rect.left;
    this.dragData.startY = e.pageY - rect.top;
    (this.dragData as ScaleData).direction = direction;
    this.scaleActive = true;
    this.registerMouseListeners();
    this.scaleStart$.next({ ids: this.ids, data: this.dragData as ScaleData, e: e });
  }
  onMouseMove(e) {
    const rect = this.wrapperEl.nativeElement.getBoundingClientRect();
    this.dragData.deltaX = e.pageX - rect.left - this.dragData.startX;
    this.dragData.deltaY = e.pageY - rect.top - this.dragData.startY;

    if (this.dragActive) {
      this.drag$.next({ ids: this.ids, data: this.dragData as DragData, elementButton: this.elementButton, e: e });
    }
    if (this.scaleActive) {
      this.scale$.next({ ids: this.ids, data: this.dragData as ScaleData, e: e });
    }
  }

  onMouseUp(e) {
    if (this.dragActive) {
      this.dragEnd$.next({ ids: this.ids, data: this.dragData as DragData, elementButton: this.elementButton, e: e });
    }
    if (this.scaleActive) {
      this.scaleEnd$.next({ ids: this.ids, data: this.dragData as ScaleData, e: e });
    }
    this.elementButton = null;
    this.dragActive = false;
    this.scaleActive = false;
    this.cleanUpMouseListeners();
    this.cd.markForCheck();
  }

  registerMouseListeners() {
    this.cleanUpMouseListeners();
    this.mouseUpListener = this.renderer.listen("window", "mouseup", (e) => this.onMouseUp(e));
    this.mouseMoveListener = this.renderer.listen("window", "mousemove", (e) => this.onMouseMove(e));
  }

  cleanUpMouseListeners() {
    if (this.mouseUpListener) {
      this.mouseUpListener();
    }
    if (this.mouseMoveListener) {
      this.mouseMoveListener();
    }
  }

  ngOnDestroy(): void {
    if (this.scaleActive) {
      this.scaleEnd$.next({ ids: this.ids, data: this.dragData as ScaleData, e: null });
    }
    if (this.dragActive) {
      this.dragEnd$.next({ ids: this.ids, data: this.dragData as DragData, elementButton: this.elementButton, e: null });
    }
    this.cleanUpMouseListeners();
  }
}
