import { InteractionBase } from "./interaction-base";
import { PluginInteractionConfig } from "../../interfaces";

export class Keydown extends InteractionBase {
  onCreate(id: string, interaction: PluginInteractionConfig) {
    this.registerListener(this.context.pluginComponentRef.location.nativeElement, "keydown", (e) => {
      const data = { code: e.code, key: e.key, which: e.which, location: e.location };
      if (interaction.config?.preventDefault) {
        e.preventDefault();
      }
      if (interaction.config?.keys) {
        if (interaction.config.keys.indexOf(e.key.toLowerCase()) >= 0) {
          const sub = this.fire(data).subscribe();
           // sub.unsubscribe();
        }
      } else {
        this.fire(data).subscribe();
      }
    });
  }
  onUpdate() {}
  onDestroy() {}
}
