import { LayoutElementState } from "plugins/src/group/group.interface";

export const move = (a: LayoutElementState, dx: number, dy: number) => {
  a.left += dx;
  a.top += dy;
};
export const isCollision = (a: LayoutElementState, b: LayoutElementState) => {
  const xa0 = getX0(a);
  const xa1 = getX1(a);
  const ya0 = getY0(a);
  const ya1 = getY1(a);
  const xb0 = getX0(b);
  const xb1 = getX1(b);
  const yb0 = getY0(b);
  const yb1 = getY1(b);
  return xa0 <= xb1 && xb0 <= xa1 && ya0 <= yb1 && yb0 <= ya1;
};
export const getX0 = (e: LayoutElementState) => {
  return e.left;
};
export const getY0 = (e: LayoutElementState) => {
  return e.top;
};
export const getX1 = (e: LayoutElementState) => {
  return e.left + e.width - 1;
};
export const getY1 = (e: LayoutElementState) => {
  return e.top + e.height - 1;
};
export const addX0 = (e: LayoutElementState, x0: number) => {
  e.left += x0;
  e.width -= x0;
};
export const addY0 = (e: LayoutElementState, y0: number) => {
  e.top += y0;
  e.height -= y0;
};
export const addX1 = (e: LayoutElementState, x1: number) => {
  e.width += x1;
};
export const addY1 = (e: LayoutElementState, y1: number) => {
  e.height += y1;
};
export const addAxis = (e: LayoutElementState, axisX: boolean, x0: number, y0: number, x1: number, y1: number) => {
  if (axisX) {
    addX0(e, x0);
    addX1(e, x1);
  } else {
    addY0(e, y0);
    addY1(e, y1);
  }
};
export const getCoordinates = (e: LayoutElementState) => {
  return { x0: getX0(e), y0: getY0(e), x1: getX1(e), y1: getY1(e) };
};
export const setCoordinates = (e: LayoutElementState, coordinates: {}) => {
  if (e === undefined) return;
  e.left = coordinates["x0"];
  e.top = coordinates["y0"];
  e.width = coordinates["x1"] - coordinates["x0"] + 1;
  e.height = coordinates["y1"] - coordinates["y0"] + 1;
};
