import { Injectable } from "@angular/core";
import { BehaviorSubject, Subject } from "rxjs";
import { MenuItem } from "../interfaces";

@Injectable({
  providedIn: "root",
})
export class UserService {
  userLoggedIn: BehaviorSubject<boolean> = new BehaviorSubject(false);

  private _sid = "";
  firstname = "";
  lastname = "";

  menu: BehaviorSubject<MenuItem[]> = new BehaviorSubject([]);
  bottomButtons: BehaviorSubject<any[]> = new BehaviorSubject([]);

  constructor() {}

  init() {
    const storedSid = localStorage.getItem("sid");
    if (storedSid) {
      this.sid = storedSid;
    }
  }

  public set sid(v: string) {
    this._sid = v;
    if (this.sid) {
      localStorage.setItem("sid", this.sid);
      this.setLoginState(true);
    } else {
      this.logout();
    }
  }

  public get sid(): string {
    return this._sid;
  }

  setLoginState(bool: boolean) {
    if (bool) {
      this.userLoggedIn.next(true);
    }
  }

  setMenu(menu) {
    this.menu.next(menu);
  }

  setBottomButtons(buttons) {
    this.bottomButtons.next(buttons);
  }

  logout() {
    this._sid = "";
    localStorage.removeItem("sid");
    this.firstname = "";
    this.lastname = "";
    this.userLoggedIn.next(false);
  }
}
