import { InteractionBase } from "./interaction-base";
import { PluginWrapperComponent } from "../plugin-wrapper.component";
import { DragData, PluginInteractionConfig } from "../../interfaces";

export class Drop extends InteractionBase {
  onCreate(id: string, interaction: PluginInteractionConfig) {
    this.registerListener(this.context.pluginComponentRef.location.nativeElement, "dragover", (e) => {
      // e.dataTransfer.dropEffect = "link"
      e.preventDefault();
    });

    this.registerListener(this.context.pluginComponentRef.location.nativeElement, "drop", (e) => {
      e.preventDefault();

      const d: { data: DragData; allowedDropTargets: string[] } = JSON.parse(e.dataTransfer.getData("text"));
      if (!d) {
        console.warn("No drag data received on drop to following target:", this.context.pluginConfig.id);
        return;
      }
      const allowedDropTargets = d.allowedDropTargets;
      if (!allowedDropTargets || allowedDropTargets.indexOf(interaction.config.dropTargetId) < 0) {
        console.warn("Dropped data not allowed for this drop target:", this.context.pluginConfig.id, "data:", d);
        return;
      }
      let data = d.data;
      if (!data) {
        data = {};
      }
      data.dropLocation = { x: e.offsetX, y: e.offsetY };
      data.dropTargetWidth = e.target.offsetWidth;
      data.dropTargetHeight = e.target.offsetHeight;
      this.fire(data).subscribe();
      // e.target.appendChild(document.getElementById(data));
    });
  }
  onUpdate() {}
  onDestroy() {}
}
