import { getPath } from "../utils";
import { InteractionBase } from "./interaction-base";
import { PluginWrapperComponent } from "../plugin-wrapper.component";
import { DragData, PluginInteractionConfig } from "../../interfaces";

export class Drag extends InteractionBase {
  onCreate(id: string, interaction: PluginInteractionConfig) {
    this.context.pluginComponentRef.location.nativeElement.setAttribute("draggable", true);
    this.registerListener(this.context.pluginComponentRef.location.nativeElement, "drag", (e) => {
      e.preventDefault();
    });

    this.registerListener(this.context.pluginComponentRef.location.nativeElement, "dragstart", (e) => {
      const data: DragData = {
        draggedElementId: this.context.pluginConfig.id,
        draggedElementPath: getPath(this.context),
        draggedElementvalue: this.context.control.value,
      };
      const d = {
        data,
        allowedDropTargets: interaction.config.allowedDropTargets,
      };

      e.dataTransfer.setData("text", JSON.stringify(d));
      if (interaction) {
        this.fire({ dragEvent: "dragstart" }).subscribe();
      }
    });

    this.registerListener(this.context.pluginComponentRef.location.nativeElement, "dragend", (e) => {
      if (interaction) {
        this.fire({ dragEvent: "dragend" }).subscribe();
      }
    });
  }
  onUpdate() {}
  onDestroy() {
    this.context.pluginComponentRef.location.nativeElement.removeAttribute("draggable");
  }
}
