import { InteractionBase } from "./interaction-base";
import { PluginInteractionConfig } from "../../interfaces";

export class Click extends InteractionBase {
  onCreate(id: string, interaction: PluginInteractionConfig) {
    this.registerListener(this.context.pluginComponentRef.location.nativeElement, "click", () => {
      this.fire().subscribe();
    });
  }
  onUpdate() {}
  onDestroy() {}
}
