import { INTERACTION_HANDLERS } from "./constants/interaction-handlers";
import { PluginDefinition, PluginConfig, PluginBackendRequest, PluginAction, PluginUpdate, CompareResult } from "./../interfaces";
import {
  Component,
  OnInit,
  Input,
  ComponentFactoryResolver,
  Injector,
  ViewChild,
  ViewContainerRef,
  ElementRef,
  HostBinding,
  ComponentRef,
  Renderer2,
  OnDestroy,
  InjectionToken,
  AfterViewInit,
} from "@angular/core";
import { PluginInteractionConfig } from "../interfaces";
import { AbstractControl, FormControl, FormGroup, FormArray } from "@angular/forms";
import { getPath } from "./utils";
import { ConfigUtilsService } from "../services/config-utils.service";
import { ActionService } from "../services/action.service";
import { ConfigService } from "../services/config.service";
import { ApiService } from "../services/api.service";
import { PingService } from "../services/ping.service";
import { ThemeService } from "../services/theme.service";
import { UserService } from "../services/user.service";
import { FormatService } from "../services/format.service";
import { AbstractPlugin } from "plugins";
import { Subject, Observable, forkJoin, BehaviorSubject } from "rxjs";
import { InteractionBase } from "./interactions/interaction-base";
import { takeUntil } from "rxjs/operators";
import { PluginInteraction } from "./interactions/plugin";
import { ResizeSensor } from "css-element-queries";
import { getDiff } from "../utils/diff";
import { deepCompare } from "../utils/deepcompare";
import * as _ from "lodash";
import { KeyService } from "../services/key.service";

export const PLUGIN_CONTEXT = new InjectionToken<string>("PluginContext");

@Component({
  selector: "app-plugin-wrapper",
  templateUrl: "./plugin-wrapper.component.html",
  styleUrls: ["./plugin-wrapper.component.scss"],
})
export class PluginWrapperComponent implements OnInit, AfterViewInit, OnDestroy {
  @HostBinding("style") containerStyle = {};
  @HostBinding("class") containerClasses;

  @ViewChild("targetRef", { read: ViewContainerRef, static: true })
  vcRef: ViewContainerRef;
  control: AbstractControl;

  @Input() pluginConfig: PluginConfig | any = {};

  @Input() pluginCreator: any;
  @Input() pluginLoader: any;

  @Input() actionHandler: any;
  @Input() parentContext: PluginWrapperComponent;
  @Input() rootContext: PluginWrapperComponent = this;
  @Input() wrapperComponentRef: ComponentRef<PluginWrapperComponent>;

  redraw$: Subject<any> = new Subject();
  contentChanged$: Subject<any> = new Subject();
  resize$: Subject<any> = new Subject();

  update$: Subject<PluginUpdate> = new Subject();
  init$: Subject<any> = new Subject();
  destroy$: Subject<any> = new Subject();
  viewinit$: BehaviorSubject<boolean> = new BehaviorSubject(false);

  pluginInteractionApplied$: Subject<PluginInteractionConfig> = new Subject();
  pluginInteractionDestroyed$: Subject<PluginInteractionConfig> = new Subject();

  id: string;
  path: string;
  pluginComponentRef: ComponentRef<AbstractPlugin>;
  pluginDefinition: PluginDefinition;
  interactions: { [id: string]: InteractionBase } = {};
  rs: ResizeSensor;

  focusElements: HTMLElement[] = [];

  wrapperInteractionHandlers: { [id: string]: new () => InteractionBase } = {};
  pluginInteractionHandlers: { [id: string]: new () => InteractionBase } = {};

  constructor(
    public injector: Injector,
    public cfr: ComponentFactoryResolver,
    public el: ElementRef,
    public action: ActionService,
    public api: ApiService,
    public key: KeyService,
    public config: ConfigService,
    public configUtils: ConfigUtilsService,
    public ping: PingService,
    public theme: ThemeService,
    public user: UserService,
    public format: FormatService,
    public renderer: Renderer2
  ) {
    this.wrapperInteractionHandlers = _.cloneDeep(INTERACTION_HANDLERS);
  }

  ngOnInit(): void {}
  ngAfterViewInit(): void {
    this.viewinit$.next(true);
  }

  setPlugin(id, pluginConfig: PluginConfig) {
    this.reset();
    this.id = id;
    this.path = _.isNil(this.path) ? (this.parentContext ? this.parentContext.path + "." + id : id) : this.path;

    const prevConfig = _.cloneDeep(this.pluginConfig);
    const diff: CompareResult = deepCompare(prevConfig, pluginConfig);
    this.pluginConfig = _.cloneDeep(pluginConfig);
    const createdPlugin = this.pluginCreator.createPlugin(this.pluginConfig, this.vcRef, this.cfr, this.injector, this);
    this.pluginComponentRef = createdPlugin.componentRef;
    this.pluginDefinition = createdPlugin.pluginDefinition;

    this.createControl();
    this.processPluginConfig(prevConfig, this.pluginConfig, diff);

    this.pluginComponentRef.changeDetectorRef.detectChanges();

    // needed for proper dimensions calculation for the grouplayout system
    this.renderer.addClass(this.pluginComponentRef.location.nativeElement, "app-d-block");

    this.rs = new ResizeSensor(this.pluginComponentRef.location.nativeElement, () => {
      this.contentChanged$.next();
    });
    this.focusElements = this.pluginComponentRef.location.nativeElement.querySelectorAll("[data-focus-target]");
    this.focusElements.forEach(() => {
      // console.log(el.getAttribute("data-focus-target"));
    });

    this.viewinit$.pipe(takeUntil(this.destroy$)).subscribe((viewinited) => {
      if (viewinited) {
        this.action.setFocus.pipe(takeUntil(this.destroy$)).subscribe((focusData) => {
          if (focusData.path === this.path) {
            console.log(focusData);
            if (Number.isInteger(focusData.value) && this.focusElements[focusData.value]) {
              this.focusElements[focusData.value].focus();
            } else if (this.focusElements[0]) {
              this.focusElements[0].focus();
            }
          }
        });
      }
    });
    // console.log(this.focusElements);
  }

  updateStyles(prevStyles, newStyles) {
    const diffs = deepCompare(prevStyles, newStyles);
    Object.keys(diffs.deleted).forEach((prop) => {
      this.renderer.removeStyle(this.pluginComponentRef.location.nativeElement, prop);
    });
    if (Object.keys(diffs.new).length > 0) {
      Object.keys(newStyles).forEach((prop) => {
        this.renderer.setStyle(this.pluginComponentRef.location.nativeElement, prop, newStyles[prop]);
      });
    }
  }
  updateClasses(prevClasses, newClasses) {
    const diffs = deepCompare(prevClasses, newClasses);
    Object.keys(diffs.deleted).forEach((prop) => {
      this.renderer.removeClass(this.pluginComponentRef.location.nativeElement, prop);
    });
    if (Object.keys(diffs.new).length > 0) {
      newClasses.forEach((className) => {
        this.renderer.addClass(this.pluginComponentRef.location.nativeElement, className);
      });
    }
  }
  updatePlugin(newConfig: PluginConfig) {
    const prevConfig = _.cloneDeep(this.pluginConfig);
    const commands = newConfig.commands ? newConfig.commands : {};
    delete newConfig.commands;
    this.pluginConfig = newConfig;

    const compareResult: CompareResult = deepCompare(prevConfig, this.pluginConfig);
    if (compareResult.diff.type) {
      console.warn("Plugin type is not allowed to be updated, please destroy and recreate instead: ", this.id);
      return;
    }

    this.processPluginConfig(prevConfig, newConfig, compareResult);
    this.update$.next({ prevConfig, newConfig, compareResult, commands });
  }

  processPluginConfig(prevConfig: PluginConfig, newConfig: PluginConfig, compareResult: CompareResult) {
    const diff = compareResult.diff;
    if (diff.containerStyle) {
      this.containerStyle = this.pluginConfig.containerStyle;
    }
    if (diff.containerClasses) {
      this.containerClasses = this.pluginConfig.containerClasses;
    }
    if (diff.style) {
      this.updateStyles(prevConfig.style, newConfig.style);
    }
    if (diff.classes) {
      this.updateClasses(prevConfig.classes, newConfig.classes);
    }

    if (diff.theme) {
      if (newConfig.theme) {
        this.theme.applyColorVars(this.el.nativeElement, newConfig.theme);
      } else {
        this.theme.removeColorVars(this.el.nativeElement);
      }
    }
    if (diff.value) {
      this.control.setValue(newConfig.value);
    }
    if (diff.interactions) {
      const diffs = deepCompare(prevConfig.interactions, newConfig.interactions);
      Object.keys(diffs.new).forEach((id) => {
        this.applyInteraction(id, newConfig.interactions[id]);
      });

      Object.keys(diffs.deleted).forEach((id) => {
        if (this.interactions[id]) {
          this.interactions[id].destroy();
        }
        delete this.interactions[id];
      });

      Object.keys(diffs.updated).forEach((id) => {
        this.applyInteraction(id, newConfig.interactions[id]);
      });
    }
  }

  registerInteraction(id, handlerClass) {
    this.pluginInteractionHandlers[id] = handlerClass;
  }
  overrideInteraction(id, handlerClass) {
    this.wrapperInteractionHandlers[id] = handlerClass;
  }
  applyInteraction(id: string, interaction: PluginInteractionConfig, ...args: any[]) {
    if (this.interactions[id]) {
      this.interactions[id].destroy();
    }
    if (this.wrapperInteractionHandlers[interaction.type]) {
      let funcClass: new () => InteractionBase = this.wrapperInteractionHandlers[interaction.type];
      if (interaction.type === "plugin") {
        if (this.pluginInteractionHandlers[interaction.pluginTrigger]) {
          funcClass = this.pluginInteractionHandlers[interaction.pluginTrigger];
        } else {
          funcClass = this.wrapperInteractionHandlers[interaction.type];
        }
      } else {
        funcClass = this.wrapperInteractionHandlers[interaction.type];
      }

      const func = new funcClass();
      func.pluginInstance = this.pluginComponentRef.instance;
      func.create(id, this, interaction, ...args);
      this.interactions[id] = func;
    } else {
      console.warn("Unknown functionality:", interaction.type);
    }
  }
  removeInteraction(id: string) {
    if (this.interactions[id]) {
      this.interactions[id].destroy();
      delete this.interactions[id];
    }
  }
  // registerInteractionHandler(id: string, handler: new () => InteractionBase) {
  //   INTERACTION_HANDLERS[this.pluginConfig.type + "_" + id] = handler;
  // }
  getPluginInteractions(pluginInteraction: string = undefined): { [id: string]: PluginInteraction } {
    const interactions = {};
    Object.keys(this.interactions).forEach((key) => {
      const interactionConfig = this.interactions[key].interaction;
      if (
        interactionConfig.type === "plugin" &&
        (interactionConfig.pluginTrigger === pluginInteraction || pluginInteraction === undefined)
      ) {
        interactions[key] = this.interactions[key];
      }
    });
    return interactions;
  }

  firePluginInteractions(type: string, data: any = null) {
    const interactions = this.getPluginInteractions(type);
    const observables: Observable<any>[] = [];
    Object.keys(interactions).forEach((key) => {
      const interaction = interactions[key];
      observables.push(interaction.fire(data).pipe(takeUntil(this.destroy$)));
    });
    return forkJoin(observables);
  }

  fireActions(
    id: string | string[],
    actions: PluginAction[],
    initiatedBy = null,
    interaction: PluginInteractionConfig = null,
    actionData = {}
  ) {
    // const promises: Promise<any>[] = [];
    const observables: Observable<any>[] = [];
    actions.forEach((action: PluginAction) => {
      const requestBody: PluginBackendRequest = {
        initiatedBy: initiatedBy ? initiatedBy : getPath(this, id),
        interactionDefinition: interaction,
        action,
        actionData,
        rootPluginConfig: this.rootContext.pluginConfig,
        rootPluginValue: {
          [this.rootContext.id]: this.rootContext.control.value,
        },
        customVars: this.action.customVars,
        modifierKeys: this.key.activeModifierKeys,
        queryParams: this.action.queryParams,
      };
      console.log(requestBody);
      const observable = this.actionHandler(requestBody).pipe(takeUntil(this.destroy$));
      observables.push(observable);
    });
    return forkJoin(observables);
  }

  createControl() {
    if (this.pluginDefinition.controlType === "group") {
      this.control = new FormGroup({});
    } else if (this.pluginDefinition.controlType === "array") {
      this.control = new FormArray([]);
    } else {
      this.control = new FormControl();
    }
    if (this.parentContext) {
      (this.parentContext.control as FormGroup).addControl(this.id, this.control);
    }
  }
  removeControl() {
    if (this.parentContext && (this.parentContext.control as FormGroup).get(this.id)) {
      (this.parentContext.control as FormGroup).removeControl(this.id);
    }
    if (this.control) {
      this.control.reset();
    }
  }

  // getChild(path: string): PluginWrapperComponent {
  //   const split = path.split('.');
  //   if (split.length > 1) {
  //     split.shift();
  //     return this.pluginComponentRef.instance.getChild(split.join('.'))
  //   } else {
  //     return this;
  //   }
  // }

  reset() {
    this.destroyPlugin();
    this.containerClasses = [];
    this.containerStyle = [];
    Object.keys(this.interactions).forEach((id) => {
      this.interactions[id].destroy();
      delete this.interactions[id];
    });
    this.path = undefined;
    this.id = undefined;
    this.pluginConfig = {};
    this.focusElements = [];
    this.theme.removeColorVars(this.el.nativeElement);
    this.removeControl();
    this.wrapperInteractionHandlers = _.cloneDeep(INTERACTION_HANDLERS);
    this.pluginInteractionHandlers = {};
  }

  destroyPlugin() {
    if (this.rs) {
      this.rs.detach();
    }
    if (this.pluginComponentRef) {
      this.destroy$.next();
      this.pluginComponentRef.destroy();
    }
    // this.vcRef.clear();
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroyPlugin();
  }
}
