import { Injectable, Optional, SkipSelf } from "@angular/core";

@Injectable()
//   {
//   providedIn: 'root'
// }
export class ConfigUtilsService {
  num = Math.random();
  constructor(@Optional() @SkipSelf() parent?: ConfigUtilsService) {
    if (parent) {
      throw Error(
        `[ConfigUtilsService]: trying to create multiple instances,
        but this service should be a singleton.`
      );
    }
  }
}
