import * as _ from "lodash";
import { CompareResult } from "../interfaces";

export const deepCompare = (oldObj, newObj): CompareResult => {
  const diffMap: CompareResult = {
    new: {},
    updated: {},
    deleted: {},
    diff: {},
  };

  if (_.isNil(oldObj) && _.isNil(newObj)) {
    return diffMap;
  }

  if (_.isNil(newObj)) {
    Object.keys(oldObj).forEach((d) => {
      diffMap.deleted[d] = true;
      diffMap.diff[d] = true;
    });
    return diffMap;
  }

  if (_.isNil(oldObj)) {
    Object.keys(newObj).forEach((d) => {
      diffMap.new[d] = true;
      diffMap.diff[d] = true;
    });
    return diffMap;
  }

  const keys = Object.keys(oldObj).concat(Object.keys(newObj));
  for (let i = 0; i < keys.length; i++) {
    const key = keys[i];
    if (!_.isEqual(oldObj[key], newObj[key])) {
      diffMap.diff[key] = true;
      if (!oldObj.hasOwnProperty(key)) {
        diffMap.new[key] = true;
      } else if (!newObj.hasOwnProperty(key)) {
        diffMap.deleted[key] = true;
      } else {
        diffMap.updated[key] = true;
      }
    }
  }

  return diffMap;
};
