import { HttpClient } from "@angular/common/http";

import { Injectable } from "@angular/core";
import { BehaviorSubject, forkJoin } from "rxjs";
import { AppConfig, PluginDefinitions } from "../interfaces";

@Injectable({
  providedIn: "root",
})
export class ConfigService {
  configLoaded: BehaviorSubject<boolean> = new BehaviorSubject(false);

  config: AppConfig;
  plugins: PluginDefinitions;
  //   libs: LibDefinitions;

  constructor(private http: HttpClient) {
    // this.configLoaded.subscribe((config) => {
    //     if (config) {
    //         this.config = config;
    //     }
    // });
  }

  loadConfig(configPath: string, pluginsConfigPath: string) {
    forkJoin([this.http.get(configPath), this.http.get(pluginsConfigPath)]).subscribe((result: any) => {
      this.config = result[0];
      this.plugins = result[1];
      //   this.libs = result[1].libs;
      this.configLoaded.next(true);
    });

    // this.http.get(configPath).subscribe((result: AppConfig) => {
    //     this.configLoaded.next(result);
    // });
    // this.http.get(pluginsConfigPath).subscribe((result: AppConfig) => {
    //     this.configLoaded.next(result);
    // });
  }
}
