import { PluginInteractionConfig, PluginBackendRequest } from "../interfaces";
import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { map, catchError, delay } from "rxjs/operators";
import { Subject, Observable } from "rxjs";
import { cloneDeep } from "lodash";
// import { v4 as uuidv4 } from 'uuid';

@Injectable({
  providedIn: "root",
})
export class ApiService {
  BASE_URL = "BASE URL NOT SET IN APP-CONFIG";
  LOGIN = "/login";
  BROWSE = "/browse";
  DOWNLOAD = "/download";
  BROWSER_INFO = "/browserinfo";
  REQUEST = "/request";
  PING = "/ping";

  // tabId = uuidv4();

  constructor(private http: HttpClient) {}

  init(baseUrl: string) {
    this.BASE_URL = baseUrl;
  }

  downloadFile(downloadid: string): Observable<any> {
    return this.http.post(this.BASE_URL + this.DOWNLOAD, { downloadid }, { responseType: "blob" });
  }

  list(
    view: string,
    keys: any,
    cursorpos: number,
    shift: number,
    count: number,
    index: number,
    before: number = 0,
    after: number = 0,
    customVars: any = {}
  ) {
    const dummySubject = new Subject();
    const body = {
      cursor: cursorpos.toString(),
      count: count.toString(),
      keys,
      shift: shift.toString(),
      index: index.toString(),
      table: view,
      before: before.toString(),
      after: after.toString(),
      customVars,
    };
    this.http
      .post(this.BASE_URL + this.BROWSE, body)
      .pipe(
        map((response: any) => {
          // setTimeout((params) => {
          dummySubject.next(response);
          dummySubject.complete();
          return response;
          // }, 300 + Math.random() * 1000);
        })
      )
      .subscribe(
        (params) => {},
        (err) => {
          dummySubject.error(err);
          // dummySubject.complete();
          // return response;
        }
      );

    return dummySubject;
  }

  getInfo(view: string) {
    return this.http.post(this.BASE_URL + this.BROWSER_INFO, { view });
  }

  request(requestedScreen: string, request: PluginBackendRequest) {
    return this.http.post(this.BASE_URL + this.REQUEST + "/" + requestedScreen, request);
    // .pipe(delay(3000), map((val) => {
    //   // try {
    //   //   val['updateScreen']["elements"][0]['_forceRefresh'] = true;

    //   // } catch (error) {

    //   // }
    //   return val;
    // }));
  }
  getRequest(requestedScreen: string) {
    return this.http.get(this.BASE_URL + this.REQUEST + "/" + requestedScreen);
  }

  ping(screenName) {
    return this.http.post(this.BASE_URL + this.PING, { screenName });
  }
}
