import { Injectable } from "@angular/core";
import * as tinycolor from "tinycolor2";
export interface Color {
  name: string;
  hex: string;
  darkContrast: boolean;
}

@Injectable({
  providedIn: "root",
})
export class ThemeService {
  id = Math.random();

  colors: { [id: string]: string } = {};
  // colors: string[] = ['#bb00ff', '#0000aa', '#11aaaa', '#000000', '#ffffff']
  // names: string[] = ['primary', 'accent', 'warn', 'foreground', 'background']
  palettes: any = {};
  cssVars: any = {};
  constructor() {
    // setInterval((params) => {
    //   for (let i = 0; i < this.colors.length; i++) {
    //     this.colors[i] = tinycolor.random().toHexString();
    //     this.updateColors();
    //   }
    // }, 2000)
    let a = 1;
    // document.addEventListener("dblclick", () => {
    //   if (a % 2) {
    //     this.colors = {
    //       primary: "#5434e3",
    //       accent: "#ed7300",
    //       warn: "#11aaaa",
    //       foreground: "#2f3d47",
    //       background: "#f3f2f5",
    //     };
    //   } else {
    //     this.colors = {
    //       primary: "#863bff",
    //       accent: "#ffdd30",
    //       warn: "#11aaaa",
    //       foreground: "#ffffff",
    //       background: "#482196",
    //     };
    //   }
    //   a++;
    //   this.updateColors();
    // });
    this.updateColors();
  }

  init(colors: { [id: string]: string }) {
    this.setColors(colors);
  }

  setColors(colors: { [id: string]: string }) {
    this.colors = colors;
    this.updateColors();
  }

  getPalettes(colors: { [id: string]: string }) {
    const palettes: any = {};
    for (const key of Object.keys(colors)) {
      palettes[key] = this.computeColors(colors[key]);
    }
    return palettes;
  }

  getCssVars(palettes: any) {
    const cssVars = [];
    for (const colorname of Object.keys(palettes)) {
      const cols = palettes[colorname];
      for (const c of cols) {
        const key = `--theme-${colorname}-${c.name}`;
        const value = c.hex;
        const key2 = `--theme-${colorname}-contrast-${c.name}`;
        const value2 = c.darkContrast ? "0,0,0" : "255,255,255";
        cssVars.push({ key, value });
        cssVars.push({ key: key2, value: value2 });
      }
    }
    return cssVars;
  }

  updateCssVars(el, cssVars: { key: string; value: string }[]) {
    for (const c of cssVars) {
      el.style.setProperty(c.key, c.value);
    }
  }
  applyColorVars(el, colors: { [id: string]: string }) {
    const palettes = this.getPalettes(colors);
    const cssVars = this.getCssVars(palettes);
    this.updateCssVars(el, cssVars);
  }
  removeColorVars(el) {
    const palettes = this.getPalettes(this.colors);
    const cssVars = this.getCssVars(palettes);
    for (const c of cssVars) {
      el.style.removeProperty(c.key);
    }
  }
  updateColors() {
    // this.applyColorVars(document.documentElement, this.c)
    this.palettes = this.getPalettes(this.colors);
    this.cssVars = this.getCssVars(this.palettes);
    this.updateCssVars(document.documentElement, this.cssVars);
  }

  computeColors(hex: string): Color[] {
    return [
      // this.getColorObject(tinycolor(hex).lighten(52), "50"),
      // this.getColorObject(tinycolor(hex).lighten(37), "100"),
      // this.getColorObject(tinycolor(hex).lighten(26), "200"),
      // this.getColorObject(tinycolor(hex).lighten(12), "300"),
      // this.getColorObject(tinycolor(hex).lighten(6), "400"),
      this.getColorObject(tinycolor(hex).lighten(40), "50"),
      this.getColorObject(tinycolor(hex).lighten(30), "100"),
      this.getColorObject(tinycolor(hex).lighten(26), "200"),
      this.getColorObject(tinycolor(hex).lighten(12), "300"),
      this.getColorObject(tinycolor(hex).lighten(6), "400"),
      // this.getColorObject(tinycolor(hex).lighten(30), "50"),
      // this.getColorObject(tinycolor(hex).lighten(25), "100"),
      // this.getColorObject(tinycolor(hex).lighten(17), "200"),
      // this.getColorObject(tinycolor(hex).lighten(12), "300"),
      // this.getColorObject(tinycolor(hex).lighten(6), "400"),
      this.getColorObject(tinycolor(hex), "500"),
      this.getColorObject(tinycolor(hex).darken(6), "600"),
      this.getColorObject(tinycolor(hex).darken(12), "700"),
      this.getColorObject(tinycolor(hex).darken(18), "800"),
      this.getColorObject(tinycolor(hex).darken(24), "900"),
      this.getColorObject(tinycolor(hex).lighten(50).saturate(30), "A100"),
      this.getColorObject(tinycolor(hex).lighten(30).saturate(30), "A200"),
      this.getColorObject(tinycolor(hex).lighten(10).saturate(15), "A400"),
      this.getColorObject(tinycolor(hex).lighten(5).saturate(5), "A700"),
    ];
  }

  getColorObject(value, name): Color {
    const c = tinycolor(value);
    return {
      name,
      hex: c.toRgbString().slice(4, -1),
      darkContrast: c.isLight(),
    };
  }
}
