import { PluginConfig } from "./../interfaces";
import { COMMAND_PREFIX } from "./constants/commands";
import { PluginWrapperComponent } from "./plugin-wrapper.component";
import * as _ from "lodash";

export const getPath = (context: PluginWrapperComponent, path: any = true) => {
  path = { [context.id]: path };
  if (context.parentContext) {
    return getPath(context.parentContext, path);
  } else {
    return path;
  }
};
export const removeCommandsFromPluginConfig = (pluginConfig) => {
  for (const key of Object.keys(pluginConfig)) {
    if (key.startsWith(COMMAND_PREFIX)) {
      delete pluginConfig[key];
    }
  }
};

