import { LayoutConfig, ElementButton, LayoutElementConfig } from "./../../group.interface";
import { ScaleData, Dimensions, DragData, Layout, LayoutState, LayoutElementState } from "../../group.interface";
import { Subject } from "rxjs";
import { LayoutElement } from "../../group-layout";
import { round, cloneDeep } from "lodash";
import { ElementRef, Renderer2, ComponentRef } from "@angular/core";
import {
  addAxis,
  addX0,
  addX1,
  addY0,
  addY1,
  getCoordinates,
  getX0,
  getX1,
  getY0,
  getY1,
  isCollision,
  move,
  setCoordinates,
} from "./utils";
import { LayoutElementControlsComponent } from "../layout-element-controls/layout-element-controls.component";
import { CompareResult } from "shared";

export class GroupLayoutPositionController {
  renderer: Renderer2;

  listOfRenderedElements: { [id: string]: LayoutElement };
  wrapperEl: ElementRef;

  controlComponent: ComponentRef<LayoutElementControlsComponent>;

  layout: Layout;
  layoutConfig: LayoutConfig;

  activeState: LayoutState;
  nextState: LayoutState;

  updateState$: Subject<any> = new Subject();
  commitState$: Subject<any> = new Subject();

  commitLayout$: Subject<any> = new Subject();

  virtualW = 1000;
  virtualH = 1000;

  dimensions: Dimensions;

  id: string;

  tempDiv;
  tempData: {
    pointerX?: number;
    pointerY?: number;
    background?: string;
    startPos?: { x0: number; y0: number; x1: number; y1: number };
    width?: number;
    height?: number;
  } = { pointerX: 0, pointerY: 0 };

  initState(dimensions: Dimensions) {
    // console.log(this.layoutConfig);
    // this.layoutConfig = this.layout["@@setup"] as any;
    this.dimensions = dimensions;
    this.redrawLayout();
  }

  redrawLayout() {
    console.log("-> redraw");
    const allInitialized = true;
    Object.keys(this.layout).forEach((key: string) => {
      const elActiveState = this.activeState[key];
      const elLayoutConfig: LayoutElementConfig = this.layout[key];
      // const coefficientW = this.dimensions.width / this.virtualW;
      // const coefficientH = this.dimensions.height / this.virtualH;
      if (elLayoutConfig["initialRedraw"] === undefined) {
        this.nextState[key] = this.createStateFromConfig(elLayoutConfig, elActiveState);
        elLayoutConfig["initialRedraw"] = true;
        console.log("Initial:", key);
      }
    });
    this.commitState$.next(this.nextState);
    console.log("<- redraw");
  }

  createStateFromConfig(elLayoutConfig, initialState) {
    const elNextState = cloneDeep(initialState);
    const coefficientH = 1;
    const coefficientW = 1;
    elNextState.left = elLayoutConfig.x * coefficientW;
    elNextState.top = elLayoutConfig.y * coefficientH;
    if (elLayoutConfig.w !== undefined) {
      elNextState.width = elLayoutConfig.w * coefficientW;
    } else {
      elNextState.width = initialState.renderedContentWidth;
    }
    if (elLayoutConfig.h !== undefined) {
      elNextState.height = elLayoutConfig.h * coefficientH;
    } else {
      elNextState.height = initialState.renderedContentHeight;
    }
    return elNextState;
  }

  onElementAdded(id: string, elLayoutConfig: LayoutElementConfig, initialState: LayoutElementState) {
    this.layout[id] = elLayoutConfig;
    this.nextState[id] = this.createStateFromConfig(elLayoutConfig, initialState);
    elLayoutConfig["initialRedraw"] = true;
  }
  onElementUpdated(id: string, elLayoutConfig: LayoutElementConfig) {
    this.layout[id] = elLayoutConfig;
    const elActiveState = this.activeState[id];
    this.nextState[id] = this.createStateFromConfig(elLayoutConfig, elActiveState);
  }
  onElementRemoved(id: string) {
    delete this.nextState[id];
    delete this.layout[id];
  }

  onLayoutChange(newLayout: Layout, newElements, deletedElements, updatedElements) {
    Object.keys(deletedElements).forEach((id) => {
      this.onElementRemoved(id);
    });
    Object.keys(newElements).forEach((id) => {
      this.onElementAdded(id, newLayout[id], newElements[id]);
    });
    Object.keys(updatedElements).forEach((id) => {
      this.onElementUpdated(id, newLayout[id]);
    });
    this.commitState$.next(this.nextState);
  }
  onLayoutConfigChange(newLayoutConfig: LayoutConfig, diffs: CompareResult) {
    console.log(newLayoutConfig, diffs);
  }

  onButtonEvent(id: string, data: any, elementButton: ElementButton) {
    console.log(id);
    console.log(elementButton.actionId);
  }
  onDoubleClick(id: string, data: any) {
    console.log(id);
  }
  onElementSizeChanged(id: string, size: { renderedContentWidth: number; renderedContentHeight: number }) {
    console.warn("elementSizeChanged", id, size);
    const elActiveState = this.activeState[id];
    const elNextState = this.nextState[id];
    elActiveState.renderedContentWidth = size.renderedContentWidth;
    elActiveState.renderedContentHeight = size.renderedContentHeight;
    elNextState.renderedContentWidth = size.renderedContentWidth;
    elNextState.renderedContentHeight = size.renderedContentHeight;
    this.redrawLayout();
  }

  onDragStart(id: any, data: DragData, elementButton: ElementButton = null) {
    // elementButton?.actionId

    this.prepareDragOrScale(id, data, "rgba(var(--theme-accent-500), 1)");
  }
  onDrag(id: any, data: DragData, elementButton: ElementButton = null) {
    // elementButton?.actionId

    switch (this.layoutConfig["dragMode"]) {
      case "float":
        this.dragModeFloat(id, data);
        break;
      case "pilot":
        this.dragModePilot(id, data);
        break;
      case "pilotWithScroll":
        this.dragModePilotWithScroll(id, data);
        break;
      case "simplePush":
        this.dragModeSimplePush(id, data);
        break;
      case "simple":
      default:
        this.dragModeSimple(id, data);
        break;
    }
  }
  onDragEnd(id: any, data: DragData, elementButton: ElementButton = null) {
    // elementButton?.actionId

    this.finishDragOrScale(id);
  }

  onScaleStart(id: any, data: ScaleData) {
    this.prepareDragOrScale(id, data, "rgba(var(--theme-primary-500), 1)");
  }
  onScale(id: any, data: ScaleData) {
    const pointerX = data.startX + data.deltaX; // mouse pointer
    const pointerY = data.startY + data.deltaY;
    const deltaX = pointerX - this.tempData.pointerX; // delta from previous position
    const deltaY = pointerY - this.tempData.pointerY;

    let distanceX = Math.abs(deltaX);
    let distanceY = Math.abs(deltaY);
    const directionX = Math.sign(deltaX);
    const directionY = Math.sign(deltaY);
    const peaks = this.getScaleAffectedPeaks(data.direction, directionX, directionY);
    const elNextState = this.nextState[id];
    while (distanceX + distanceY > 0) {
      const handleX = distanceX > distanceY;
      const effectiveX = handleX ? directionX : 0;
      const effectiveY = handleX ? 0 : directionY;
      if ((handleX && (peaks.x0 < 0 || 0 < peaks.x1)) || (!handleX && (peaks.y0 < 0 || 0 < peaks.y1))) {
        if (this.canMoveAndPushOthers(id, effectiveX, effectiveY) && this.canIncrease(elNextState, effectiveX, effectiveY)) {
          this.pushAllAffected(id, effectiveX, effectiveY);
          handleX ? distanceX-- : distanceY--;
          if (handleX) {
            effectiveX < 0 ? addX1(elNextState, 1) : addX0(elNextState, -1);
          } else {
            effectiveY < 0 ? addY1(elNextState, 1) : addY0(elNextState, -1);
          }
        } else {
          handleX ? (distanceX = 0) : (distanceY = 0);
        }
      } else {
        addAxis(elNextState, handleX, peaks.x0, peaks.y0, peaks.x1, peaks.y1);
        if (this.isOutOfBounds(elNextState) || !this.isScaleAble(id, effectiveX, effectiveY)) {
          handleX ? (distanceX = 0) : (distanceY = 0);
          addAxis(elNextState, handleX, -peaks.x0, -peaks.y0, -peaks.x1, -peaks.y1);
        } else {
          handleX ? distanceX-- : distanceY--;
        }
      }
    }
    this.updateLastProcessedPointers(pointerX, pointerY);
  }
  onScaleEnd(id: any, data: ScaleData) {
    this.finishDragOrScale(id);
  }

  onResize(dimensions: Dimensions) {
    this.dimensions = dimensions;
    // this.redrawLayout();
  }

  // Converts current state back to LayoutConfig for saving
  toLayout(): Layout {
    // temporary solution for testing, writing the current state back into the layout
    const clonedLayout = cloneDeep(this.layout);
    Object.keys(this.activeState).forEach((key) => {
      const state = this.activeState[key];
      clonedLayout[key].x = state.left;
      clonedLayout[key].y = state.top;
      clonedLayout[key].w = state.width;
      clonedLayout[key].h = state.height;
    });
    return clonedLayout;
  }

  infoHook(id: any): any {
    // const elActiveState = this.activeState[id];
    // const elNextState = this.nextState[id];
    // return `
    // x: ${round(elActiveState.left)} / ${round(elNextState.left)} <br>
    // y: ${round(elActiveState.top)} / ${round(elNextState.top)}<br>
    // w: ${round(elActiveState.width)} / ${round(elNextState.width)}<br>
    // h: ${round(elActiveState.height)} / ${round(elNextState.height)}
    // `;

    if (this.layout[id]?.properties?.fixedX) {
      // true ||  > 0
    } else {
      // false || undefined || null || <=0
    }

    return JSON.stringify(this.layout[id].properties);
  }

  // --------------------------------------------------------------------------- drag modes
  dragModePilot(id: any, data: DragData) {
    const elActiveState = this.activeState[id];
    const elNextState = this.nextState[id];
    elNextState.top = elActiveState.top + data.deltaY;
    elNextState.left = elActiveState.left + data.deltaX;
    this.updateState$.next(this.nextState);
  }

  dragModePilotWithScroll(id: any, data: DragData) {
    const elActiveState = this.activeState[id];
    const elNextState = this.nextState[id];
    elNextState.top = elActiveState.top + data.deltaY;
    elNextState.left = elActiveState.left + data.deltaX;

    // logic to follow dragged element with the scrollbar
    const hostEl = this.wrapperEl.nativeElement;
    if (elNextState.left + elNextState.width > hostEl.clientWidth + hostEl.scrollLeft) {
      hostEl.scrollLeft = elNextState.left + elNextState.width - hostEl.clientWidth;
    }
    if (elNextState.left < hostEl.scrollLeft) {
      hostEl.scrollLeft = elNextState.left;
    }
    if (elNextState.top + elNextState.height > hostEl.clientHeight + hostEl.scrollTop) {
      hostEl.scrollTop = elNextState.top + elNextState.height - hostEl.clientHeight;
    }
    if (elNextState.top < hostEl.scrollTop) {
      hostEl.scrollTop = elNextState.top;
    }
    this.updateState$.next(this.nextState);
  }

  dragModeSimple(id: any, data: DragData) {
    const elNextState = this.nextState[id];
    const dragContext = this.getDragContext(elNextState, data);
    while (dragContext.distanceX + dragContext.distanceY > 0) {
      const moveX = dragContext.distanceX > dragContext.distanceY ? dragContext.directionX : 0;
      const moveY = dragContext.distanceX > dragContext.distanceY ? 0 : dragContext.directionY;
      if (this.canMoveSingleElement(id, moveX, moveY)) {
        this.pushAllAffected(id, moveX, moveY);
        moveX === 0 ? dragContext.distanceY-- : dragContext.distanceX--;
      } else {
        moveX === 0 ? (dragContext.distanceY = 0) : (dragContext.distanceX = 0);
      }
    }
    this.updateLastProcessedPointers(dragContext.pointerX, dragContext.pointerY);
  }

  dragModeSimplePush(id: any, data: DragData) {
    const elNextState = this.nextState[id];
    const dragContext = this.getDragContext(elNextState, data);
    while (dragContext.distanceX + dragContext.distanceY > 0) {
      const moveX = dragContext.distanceX > dragContext.distanceY ? dragContext.directionX : 0;
      const moveY = dragContext.distanceX > dragContext.distanceY ? 0 : dragContext.directionY;
      if (this.canMoveAndPushOthers(id, moveX, moveY)) {
        this.pushAllAffected(id, moveX, moveY);
        moveX === 0 ? dragContext.distanceY-- : dragContext.distanceX--;
      } else {
        moveX === 0 ? (dragContext.distanceY = 0) : (dragContext.distanceX = 0);
      }
    }
    this.updateLastProcessedPointers(dragContext.pointerX, dragContext.pointerY);
  }

  dragModeFloat(id: any, data: DragData) {
    const elNextState = this.nextState[id];
    const dragContext = this.getDragContext(elNextState, data);
    elNextState.top += dragContext.directionY * dragContext.distanceY;
    elNextState.left += dragContext.directionX * dragContext.distanceX;

    this.renderer.setStyle(
      this.listOfRenderedElements[id].componentRef.location.nativeElement,
      "background",
      this.isOverflow(elNextState) || this.getOverlaps(id, elNextState).length !== 0
        ? "rgba(var(--theme-warn-500), 1)"
        : "rgba(var(--theme-accent-500), 1)"
    );
    this.updateLastProcessedPointers(dragContext.pointerX, dragContext.pointerY);
  }

  // --------------------------------------------------------------------------- internal methods
  prepareDragOrScale(id: any, data: DragData | ScaleData, color: string) {
    this.createTempDiv();

    this.renderer.setStyle(this.tempDiv, "width", this.nextState[id].width + "px");
    this.renderer.setStyle(this.tempDiv, "height", this.nextState[id].height + "px");
    this.renderer.setStyle(this.tempDiv, "top", this.nextState[id].top + "px");
    this.renderer.setStyle(this.tempDiv, "left", this.nextState[id].left + "px");
    this.renderer.setStyle(this.tempDiv, "z-index", 0);

    this.nextState[id].zIndex = 9999;

    this.tempData.startPos = getCoordinates(this.nextState[id]);
    this.tempData.pointerX = data.startX;
    this.tempData.pointerY = data.startY;
    this.tempData.background = this.listOfRenderedElements[id].componentRef.location.nativeElement.style.background;

    // resize delay WIP
    // this.renderer.setStyle(
    //   this.listOfRenderedElements[id].componentRef.instance.pluginComponentRef.location.nativeElement,
    //   "width",
    //   this.activeState[id].width + "px"
    // );
    // this.renderer.setStyle(
    //   this.listOfRenderedElements[id].componentRef.instance.pluginComponentRef.location.nativeElement,
    //   "height",
    //   this.activeState[id].height + "px"
    // );

    this.renderer.setStyle(this.listOfRenderedElements[id].componentRef.location.nativeElement, "background", color);
    this.renderer.setStyle(
      this.listOfRenderedElements[id].componentRef.location.nativeElement,
      "box-shadow",
      "5px 5px 8px 8px rgba(var(--theme-background-900), 0.4)"
    );
    this.commitState$.next(this.nextState);
  }

  finishDragOrScale(id: any) {
    // setCoordinates(this.nextState["@@setup"], { x0: -20, y0: -20, x1: -10, y1: -10 });
    this.cleanUpTempDiv();
    const s = this.nextState[id];
    if (this.isOverflow(s) || this.getOverlaps(id, s).length !== 0) setCoordinates(s, this.tempData.startPos);
    this.nextState[id].zIndex = 0;

    // // resize delay WIP
    // this.renderer.removeStyle(this.listOfRenderedElements[id].componentRef.instance.pluginComponentRef.location.nativeElement, "width");
    // this.renderer.removeStyle(this.listOfRenderedElements[id].componentRef.instance.pluginComponentRef.location.nativeElement, "height");

    this.renderer.setStyle(this.listOfRenderedElements[id].componentRef.location.nativeElement, "background", this.tempData.background);
    this.renderer.setStyle(
      this.listOfRenderedElements[id].componentRef.location.nativeElement,
      "box-shadow",
      "0px 0px 3px 3px rgba(var(--theme-background-900), 0)"
    );
    this.commitState$.next(this.nextState);
  }

  updateLastProcessedPointers(x, y) {
    this.tempData.pointerX = x;
    this.tempData.pointerY = y;
    this.updateState$.next(this.nextState);
  }

  canMoveAndPushOthers(id: any, sx: number, sy: number) {
    const e = this.nextState[id];
    if (!this.isMoveAble(id, sx, sy)) return false;
    move(e, sx, sy);
    const result = (() => {
      if (this.isOverflow(e)) return false;
      for (const item of this.getOverlaps(id, e)) {
        if (!this.canMoveAndPushOthers(item, sx, sy)) {
          return false;
        }
      }
      return true;
    })();
    move(e, -sx, -sy);
    return result;
  }

  canMoveSingleElement(id: any, sx: number, sy: number) {
    const e = this.nextState[id];
    if (!this.isMoveAble(id, sx, sy)) return false;
    move(e, sx, sy);
    const result = (() => {
      if (this.isOverflow(e)) {
        return false;
      } else return this.getOverlaps(id, e).length === 0;
    })();
    move(e, -sx, -sy);
    return result;
  }

  pushAllAffected(id: any, sx: number, sy: number) {
    const e = this.nextState[id];
    move(e, sx, sy);
    for (const item of this.getOverlaps(id, e)) this.pushAllAffected(item, sx, sy);
  }

  getOverlaps(id: any, e: LayoutElementState) {
    const overlaps: string[] = [];
    Object.keys(this.layout).forEach((item) => {
      if (item === "@@setup" || item === id) return;
      if (isCollision(this.nextState[item], e)) overlaps.push(item);
    });
    return overlaps;
  }

  isMoveAble(id: string, x: number, y: number) {
    // const properties = this.layout[id]["properties"] === undefined ? "" : this.layout[id]["properties"];
    // if (x !== 0 && properties.search("fixedXpos") >= 0) return false;
    // if (y !== 0 && properties.search("fixedYpos") >= 0) return false;
    return true;
  }

  isScaleAble(id: string, x: number, y: number) {
    // const properties = this.layout[id]["properties"] === undefined ? "" : this.layout[id]["properties"];
    // if (x !== 0 && (properties.search("fixedXpos") >= 0 || properties.search("fixedXsize") >= 0)) return false;
    // if (y !== 0 && (properties.search("fixedYpos") >= 0 || properties.search("fixedXsize") >= 0)) return false;
    return true;
  }

  isOverflow(e: LayoutElementState) {
    if (getY1(e) >= this.dimensions.height) return true;
    if (getY0(e) < 0) return true;
    if (getX1(e) >= this.dimensions.width) return true;
    return getX0(e) < 0;
  }

  getDragContext(e: LayoutElementState, data: DragData) {
    const pointerX = data.startX + data.deltaX; // mouse pointer
    const pointerY = data.startY + data.deltaY;
    let deltaX = pointerX - this.tempData.pointerX; // delta from previous position
    let deltaY = pointerY - this.tempData.pointerY;

    // centering if disconnected
    if (this.tempData.pointerX < getX0(e) || getX1(e) < this.tempData.pointerX) {
      deltaX = this.getCenteringCorrection(deltaX, pointerX, getX0(e), getX1(e));
    }
    if (this.tempData.pointerY < getY0(e) || getY1(e) < this.tempData.pointerY) {
      deltaY = this.getCenteringCorrection(deltaY, pointerY, getY0(e), getY1(e));
    }

    // slippery area handling
    const slipperyArea = this.layoutConfig["slipperyArea"];
    if (slipperyArea > 0) {
      if (e.width >= 2 * slipperyArea + 10 && getX0(e) <= pointerX && pointerX <= getX1(e)) {
        deltaX = this.getSlipperyCorrection(deltaX, pointerX, getX0(e), getX1(e), slipperyArea);
      }
      if (e.height >= 2 * slipperyArea + 10 && getY0(e) <= pointerY && pointerY <= getY1(e)) {
        deltaY = this.getSlipperyCorrection(deltaY, pointerY, getY0(e), getY1(e), slipperyArea);
      }
    }

    return {
      distanceX: Math.abs(deltaX),
      distanceY: Math.abs(deltaY),
      directionX: Math.sign(deltaX),
      directionY: Math.sign(deltaY),
      pointerX,
      pointerY,
    };
  }

  getCenteringCorrection(delta: number, pointer: number, start: number, end: number) {
    return pointer < start || end < pointer ? Math.round(pointer - (start + end) / 2) : delta;
  }

  getSlipperyCorrection(delta: number, pointer: number, start: number, end: number, slipperyArea: number) {
    if (delta > 0) {
      return delta - Math.min(Math.max(0, slipperyArea - (pointer - start)), delta);
    } else if (delta < 0) {
      return delta + Math.min(Math.max(0, slipperyArea - (end - pointer)), -delta);
    }
    return delta;
  }

  getScaleAffectedPeaks(direction: "n" | "s" | "w" | "e" | "nw" | "ne" | "sw" | "se", directionX: number, directionY: number) {
    let x0 = 0;
    let x1 = 0;
    let y0 = 0;
    let y1 = 0;
    switch (direction) {
      case "n":
        y0 = directionY;
        break;
      case "s":
        y1 = directionY;
        break;
      case "w":
        x0 = directionX;
        break;
      case "e":
        x1 = directionX;
        break;
      case "nw":
        y0 = directionY;
        x0 = directionX;
        break;
      case "ne":
        y0 = directionY;
        x1 = directionX;
        break;
      case "sw":
        y1 = directionY;
        x0 = directionX;
        break;
      case "se":
        y1 = directionY;
        x1 = directionX;
        break;
      default:
        break;
    }
    return { x0, y0, x1, y1 };
  }

  isOutOfBounds(e: LayoutElementState) {
    return e.width < 25 || 1000 < e.width || e.height < 25 || 1000 < e.height;
  }

  canIncrease(e: LayoutElementState, x: number, y: number) {
    return x + e.width <= 1000 && y + e.height <= 1000;
  }

  createTempDiv() {
    this.tempDiv = this.renderer.createElement("div");
    this.renderer.appendChild(this.wrapperEl.nativeElement, this.tempDiv);
    this.renderer.setStyle(this.tempDiv, "background", "rgba(var(--theme-background-300), 0.3)");
    this.renderer.setStyle(this.tempDiv, "border", "1px dotted rgba(var(--theme-background-300), 0.7)");
    this.renderer.setStyle(this.tempDiv, "position", "absolute");
  }
  cleanUpTempDiv() {
    if (this.tempDiv) {
      this.renderer.removeChild(this.wrapperEl, this.tempDiv);
      this.tempDiv = null;
    }
  }
}
