import { InteractionBase } from "./interaction-base";
import { PluginWrapperComponent } from "../plugin-wrapper.component";
import { PluginInteractionConfig } from "../../interfaces";
import { takeUntil, filter } from "rxjs/operators";
import { cloneDeep, isEqual } from "lodash";

export class ValueChange extends InteractionBase {
  timeout;
  prevValue;
  onCreate(id: string, interaction: PluginInteractionConfig) {
    this.context.control.valueChanges
      .pipe(
        takeUntil(this.destroy$),
        filter((newVal) => {
          return !isEqual(this.prevValue, newVal);
        })
      )
      .subscribe((value) => {
        clearTimeout(this.timeout);
        this.timeout = setTimeout(() => {
          this.fire(value).subscribe();
        });
      });
    this.prevValue = cloneDeep(this.context.control.value);
  }
  onUpdate() {}
  onDestroy() {
    clearTimeout(this.timeout);
  }
}
