import { GroupPluginConfig, PopupOptions, Layout } from "./group.interface";
import {
  Component,
  ViewChild,
  ViewContainerRef,
  ComponentFactoryResolver,
  Injector,
  HostBinding,
  ComponentRef,
  Renderer2,
  ElementRef,
  ApplicationRef,
  ChangeDetectorRef,
  AfterViewInit,
  OnInit,
  OnDestroy,
  Inject,
} from "@angular/core";
import {
  PluginWrapperComponent,
  PLUGIN_COMMANDS,
  PLUGIN_CONTEXT,
  getDiff,
  PluginConfig,
  deepCompare,
  CompareResult,
  PluginUpdate,
} from "shared";
import { has } from "lodash";
import { Overlay, OverlayContainer, FlexibleConnectedPositionStrategy } from "@angular/cdk/overlay";
import { ComponentPortal } from "@angular/cdk/portal";
import { AbstractPlugin } from "../abstract-plugin";
import { sortElementKeysByLayout, filterLayoutElementsByType } from "./group.utils";
import { GroupLayout } from "./group-layout";
import { takeUntil } from "rxjs/operators";
import * as _ from "lodash";
import { Subject } from "rxjs";

@Component({
  selector: "app-group",
  templateUrl: "./group.component.html",
  styleUrls: ["./group.component.scss"],
})
export class GroupComponent implements AbstractPlugin, AfterViewInit, OnInit, OnDestroy {
  // @HostBinding("class.mat-app-background") background = true;

  @ViewChild("targetVC", { read: ViewContainerRef, static: true })
  targetVC: ViewContainerRef;

  @ViewChild("wrapperRef", { read: ElementRef, static: true })
  wrapperRef: ElementRef;
  @ViewChild("wrapperVC", { read: ElementRef, static: true })
  wrapperVC: ViewContainerRef;

  @ViewChild("overlayContainerRef", { read: ElementRef, static: true })
  overlayContainerRef: ElementRef;

  elements: { [id: string]: ComponentRef<PluginWrapperComponent> } = {};
  elementAdded$: Subject<any> = new Subject();
  elementRemoved$: Subject<any> = new Subject();

  layout: GroupLayout;

  @HostBinding("class.building") isBuilding = false;

  constructor(
    private cfr: ComponentFactoryResolver,
    private injector: Injector,
    private overlay: Overlay,
    private overlayContainer: OverlayContainer,
    private renderer: Renderer2,
    private elRef: ElementRef,
    private hostVC: ViewContainerRef,
    private cd: ChangeDetectorRef,
    @Inject(PLUGIN_CONTEXT) public context: PluginWrapperComponent
  ) {}

  ngOnInit(): void {
    // if (this.context.rootContext === this.context) {
    //   (this.overlayContainer as any).setContainer(this.overlayContainerRef.nativeElement);
    // }
    this.pluginOnInit();
    this.context.update$.pipe(takeUntil(this.context.destroy$)).subscribe((update: PluginUpdate) => {
      this.pluginOnUpdate(
        update.prevConfig as GroupPluginConfig,
        update.newConfig as GroupPluginConfig,
        update.compareResult,
        update.commands
      );
    });
  }

  pluginOnInit(): void {
    this.context.redraw$.pipe(takeUntil(this.context.destroy$)).subscribe(() => {
      this.layout.onResize();
    });
    this.context.contentChanged$.pipe(takeUntil(this.context.destroy$)).subscribe(() => {
      this.layout.onResize();
    });

    this.layout = new GroupLayout(
      this.context.id,
      this.context,
      this.elRef,
      this.hostVC,
      this.wrapperRef,
      this.targetVC,
      this.renderer,
      this.cfr,
      this.injector,
      this.cd,
      this.overlay
    );
    this.createPluginWrappers();
    // this.sortWrappers();
  }

  ngAfterViewInit(): void {
    let layoutConfig = this.context.pluginConfig?.config?.layoutConfig;
    layoutConfig = layoutConfig ? layoutConfig : {};

    this.layout.init(this.elements, layoutConfig);
    this.layout.buildModeEnded$.pipe(takeUntil(this.context.destroy$)).subscribe((layout: Layout) => {
      this.saveLayout(layout);
    });
    this.layout.buildMode$.pipe(takeUntil(this.context.destroy$)).subscribe((val) => {
      this.isBuilding = val;
    });
    this.layout.addNew$.pipe(takeUntil(this.context.destroy$)).subscribe(() => {
      this.addNew();
    });
  }

  saveLayout(layout: Layout) {
    this.context.firePluginInteractions("saveLayout", layout).subscribe();
    // temporary test layout merge, should be updated from the backend;
    Object.keys(layout).forEach((key) => {
      const conf = layout[key];
      // console.log(key, this.elements[key]);
      // this.elements[key].instance.pluginConfig.layout = conf;
      // this.context.pluginConfig.config.layout[key] = conf;
    });
    this.sortWrappers();
  }

  addNew() {
    // this.context.getPluginInteractions("addNew").forEach((interaction) => {
    //   interaction.fire();
    // });
  }

  sortWrappers() {
    let sortedElements = sortElementKeysByLayout(this.elements);
    sortedElements = sortedElements.filter((key) => {
      const layoutConfig = this.context.pluginConfig?.config?.layout?.[key];
      if (layoutConfig?.type === "absolute") {
        return true;
      } else {
        return false;
      }
    });
    for (let i = 0; i < sortedElements.length; i++) {
      const key = sortedElements[i];
      const el = this.elements[key];
      const viewRef = el.hostView;
      const index = this.targetVC.indexOf(viewRef);
      if (index !== i) {
        this.targetVC.detach(index);
        this.targetVC.insert(viewRef, i);
        // this.targetVC.move(viewRef, i);
      }
    }
  }

  pluginOnUpdate(prevConfig: GroupPluginConfig, newConfig: GroupPluginConfig, compareResult: CompareResult, commands: any): void {
    const diff = compareResult.diff;
    if (diff.config) {
      const configDiffs = getDiff(prevConfig.config, newConfig.config);
      // && newConfig.config.elements
      //  this.currentPluginConfig = mergeConfig(this.currentPluginConfig, rootPluginUpdate);
      if (configDiffs.elements) {
        const elementDiffs = getDiff(prevConfig.config.elements, newConfig.config.elements);
        const eDiffs = deepCompare(prevConfig.config.elements, newConfig.config.elements);

        Object.keys(eDiffs.deleted).forEach((id) => {
          this.elements[id].destroy();
          delete this.elements[id];
        });

        Object.keys(eDiffs.new).forEach((id) => {
          this.elements[id] = this.createPlugin(id, newConfig.config.elements[id]);
        });

        Object.keys(eDiffs.updated).forEach((id) => {
          this.elements[id].instance.updatePlugin(newConfig.config.elements[id]);
        });

        // for (const key of Object.keys(elementDiffs)) {
        //   const newPluginConf = newConfig.config.elements[key];
        //   const pluginWrapperRef = this.elements[key];
        //   if (pluginWrapperRef && _.isNil(newPluginConf)) {
        //     // delete
        //     pluginWrapperRef.destroy();
        //     delete this.elements[key];
        //     // this.layout.removeElement(key);
        //   } else if (!_.isNil(newPluginConf) && !pluginWrapperRef) {
        //     // new, needs to be createad
        //     this.elements[key] = this.createPlugin(key, newPluginConf);
        //     // this.layout.addElement(key, newPluginConf.layout);
        //   } else if (pluginWrapperRef) {
        //     // update
        //     pluginWrapperRef.instance.updatePlugin(newPluginConf);
        //     // this.layout.updateElement(key, newPluginConf.layout);
        //   }
        // }
        this.layout.elementsChange();
      }

      if (configDiffs.layoutConfig) {
        this.layout.layoutConfigChange(newConfig.config.layoutConfig);
      }
    }
  }

  createPluginWrappers() {
    const pluginConfig = this.context.pluginConfig as GroupPluginConfig;
    if (!pluginConfig.config || !pluginConfig.config.elements) {
      console.warn("Group type plugin does not have any child elements", this.context.pluginConfig.id);
    }

    // Create normal plugins first
    this.createPlugins(pluginConfig.config.elements);
    // Create popups plugins second
    // this.createPlugins(true, pluginConfig.config.elements)
  }

  createPlugins(elements) {
    // const sortedElements = sortElementsByLayout(elements, this.layout);

    for (const key of Object.keys(elements)) {
      const pluginConfig = elements[key];
      this.elements[key] = this.createPlugin(key, pluginConfig);
    }
  }
  createPlugin(id: string, pluginConfig: PluginConfig) {
    let componentRef;
    if (this.context.pluginConfig.config?.layout?.[id]?.type === "popup") {
      componentRef = this.createPopupRef(pluginConfig.layout[id]);
    } else {
      const componentFactory = this.cfr.resolveComponentFactory(this.context.pluginCreator.getPluginWrapperComponent());
      componentRef = this.targetVC.createComponent(componentFactory, undefined, this.injector);
    }
    const instance: PluginWrapperComponent = componentRef.instance as any;
    instance.pluginCreator = this.context.pluginCreator;
    instance.pluginLoader = this.context.pluginLoader;
    instance.parentContext = this.context;
    instance.rootContext = this.context.rootContext;
    instance.actionHandler = this.context.actionHandler;
    instance.wrapperComponentRef = componentRef;
    // instance.path = (this.context.path !== undefined ? this.context.path + "." : "") + this.context.id + "." + key;
    instance.setPlugin(id, pluginConfig);
    return componentRef;
  }

  createPopupRef(popupOptions: PopupOptions) {
    let positionStrategy: any = this.overlay.position();
    const connectedTo = this.getConnectedToElementRef(popupOptions);
    if (connectedTo) {
      positionStrategy = positionStrategy.flexibleConnectedTo(connectedTo);
      if (popupOptions.withPositions) {
        positionStrategy = ((positionStrategy as any) as FlexibleConnectedPositionStrategy).withPositions(popupOptions.withPositions);
      } else {
        positionStrategy = ((positionStrategy as any) as FlexibleConnectedPositionStrategy).withPositions([
          {
            originX: "start",
            originY: "bottom",
            overlayX: "start",
            overlayY: "top",
          },
          {
            originX: "start",
            originY: "top",
            overlayX: "start",
            overlayY: "bottom",
          },
        ]);
      }
      if (popupOptions.withPush !== undefined) {
        positionStrategy = ((positionStrategy as any) as FlexibleConnectedPositionStrategy).withPush(popupOptions.withPush);
      } else {
        positionStrategy = ((positionStrategy as any) as FlexibleConnectedPositionStrategy).withPush(true);
      }
    } else if (popupOptions.global) {
      positionStrategy = positionStrategy
        .global()
        .left(popupOptions.global.x + "px")
        .top(popupOptions.global.y + "px");
    }

    const overlayRef = this.overlay.create({
      width: popupOptions.width,
      height: popupOptions.height,
      positionStrategy,
      hasBackdrop: popupOptions.hasBackdrop,
    });
    overlayRef.backdropClick().subscribe(() => {
      overlayRef.detach();
      overlayRef.dispose();
    });
    const userProfilePortal = new ComponentPortal(this.context.pluginCreator.getPluginWrapperComponent());
    return overlayRef.attach(userProfilePortal);
  }

  getConnectedToElementRef(popupOptions) {
    return popupOptions.connectedTo ? this.elements[popupOptions.connectedTo].instance.pluginComponentRef.location : null;
  }

  ngOnDestroy(): void {
    this.layout.destroy();
  }
}
