import { Component, OnInit, ViewContainerRef, ViewChild } from "@angular/core";
import { Subject, BehaviorSubject } from "rxjs";

@Component({
  selector: "app-layout-controls",
  templateUrl: "./layout-controls.component.html",
  styleUrls: ["./layout-controls.component.scss"],
})
export class LayoutControlsComponent {
  buildMode = false;
  buildModeEntered: Subject<null> = new Subject();
  buildModeEnded: Subject<null> = new Subject();
  buildModeCancelled: Subject<null> = new Subject();
  startEdit() {
    this.buildModeEntered.next();
  }
  endEdit() {
    this.buildModeEnded.next();
  }
  cancelEdit() {
    this.buildModeCancelled.next();
  }
  openSettings() {}
  addElement() {}
}
