import { PluginWrapperComponent, getDeleted, getNew, getDiff, deepCompare } from "shared";
import {
  ComponentRef,
  Renderer2,
  ElementRef,
  ComponentFactoryResolver,
  ViewContainerRef,
  Injector,
  ChangeDetectorRef,
} from "@angular/core";
import { cloneDeep, takeWhile } from "lodash";
import { GroupLayoutPositionController } from "./layout/position-controller/group-layout-position-controller";
import { LayoutElementControlsComponent } from "./layout/layout-element-controls/layout-element-controls.component";
import { LayoutControlsComponent } from "./layout/layout-controls/layout-controls.component";
import { BehaviorSubject, Subject } from "rxjs";
import { takeUntil, distinctUntilChanged, skip, debounceTime } from "rxjs/operators";
import { Layout, LayoutElementConfig, LayoutState, LayoutConfig, LayoutElementState } from "./group.interface";
import { Overlay, OverlayRef } from "@angular/cdk/overlay";
import { ComponentPortal } from "@angular/cdk/portal";
import { LayoutElementButtonControlsComponent } from "./layout/layout-element-controls/layout-element-button-controls/layout-element-button-controls.component";
import * as _ from "lodash";
import { filterLayoutElementsByType } from "./group.utils";

export interface LayoutElement {
  componentRef: ComponentRef<PluginWrapperComponent>;
  // controlsRef?: ComponentRef<LayoutElementControlsComponent>;
  //   position: 'absolute' | 'relative' | 'fixed';
  listeners?: {
    drag?: () => void;
    scale?: () => void;
    click?: () => void;
    keydown?: () => void;
    keyup?: () => void;
    mouseover?: () => void;
  };
}

export class GroupLayout {
  ngUnsubscribe: Subject<any> = new Subject();

  layout: Layout;
  layoutConfig: LayoutConfig;
  currentLayout: Layout;
  elements: { [id: string]: ComponentRef<PluginWrapperComponent> };

  previousState: LayoutState;
  currentState: LayoutState;

  editMode$: BehaviorSubject<boolean> = new BehaviorSubject(false);
  editModeExited$: Subject<null> = new Subject();

  buildMode$: BehaviorSubject<boolean> = new BehaviorSubject(false);
  buildModeCancelled$: Subject<null> = new Subject();
  buildModeEnded$: Subject<Layout> = new Subject();

  addNew$: Subject<null> = new Subject();

  layoutElements: { [id: string]: LayoutElement } = {};
  layoutControlsRef: ComponentRef<LayoutControlsComponent>;

  viewportW = 1000;
  viewportH = 1000;

  resizeListener;
  backgroundClickListener;
  backgroundMouseOverListener;
  positionController: GroupLayoutPositionController;

  skipResizeEvent = false;

  activeButtonControl: OverlayRef;
  activeElementControls: ComponentRef<LayoutElementControlsComponent>;
  activeElementIds = [];
  activeElementEngaged = false;
  lastHoveredElement = null;

  constructor(
    public id: string,
    public context: PluginWrapperComponent,
    public hostEl: ElementRef,
    public hostVC: ViewContainerRef,
    public wrapperEl: ElementRef,
    public wrapperVC: ViewContainerRef,
    public renderer: Renderer2,
    public cfr: ComponentFactoryResolver,
    public injector: Injector,
    public cd: ChangeDetectorRef,
    private overlay: Overlay
  ) {
    this.resizeListener = this.renderer.listen("window", "resize", (e) => {
      this.onResize();
    });
    this.backgroundClickListener = this.renderer.listen(this.wrapperEl.nativeElement, "click", (e: MouseEvent) => {
      if (e.target === e.currentTarget) {
        this.deactivate();
      }
    });
    this.backgroundMouseOverListener = this.renderer.listen(this.wrapperEl.nativeElement, "mouseover", (e: MouseEvent) => {
      if (e.target === e.currentTarget && this.activeElementEngaged === false) {
        this.deactivate();
        this.setLastHovered(null);
      }
    });
  }

  // Init and creation
  init(elements: { [id: string]: ComponentRef<PluginWrapperComponent> }, layoutConfig: LayoutConfig) {
    this.elements = elements;

    const collectedLayout = {};
    Object.keys(this.elements).forEach((key) => {
      const elLayout = this.elements[key].instance.pluginConfig?.layout;
      if (!_.isNil(elLayout) && elLayout.type === "absolute") {
        collectedLayout[key] = elLayout;
      }
    });

    this.layout = collectedLayout;
    this.currentLayout = cloneDeep(this.layout);
    this.layoutConfig = cloneDeep(layoutConfig);

    this.positionController = new GroupLayoutPositionController();

    const initialState = this.initLayoutElements();
    this.positionController.listOfRenderedElements = this.layoutElements;
    this.positionController.wrapperEl = this.wrapperEl;
    this.positionController.id = this.id;
    this.positionController.layout = cloneDeep(this.layout);
    this.positionController.activeState = cloneDeep(initialState);
    this.positionController.nextState = cloneDeep(initialState);
    this.positionController.layoutConfig = cloneDeep(this.layoutConfig);
    this.positionController.renderer = this.renderer;
    this.editMode$.next(this.layoutConfig?.editMode);
    this.buildMode$.next(this.layoutConfig?.buildMode);

    this.editMode$.pipe(takeUntil(this.ngUnsubscribe), distinctUntilChanged()).subscribe((value) => {
      if (value) {
        this.createLayoutControls();
      } else {
        this.editModeExited$.next();
        this.removeLayoutControls();
      }
    });

    this.buildMode$.pipe(takeUntil(this.ngUnsubscribe), distinctUntilChanged()).subscribe((value) => {
      if (value) {
        this.enableBuildMode();
      } else {
        this.disableBuildMode();
      }
    });
    // this.positionController.updateState$.pipe(debounceTime(500), takeUntil(this.ngUnsubscribe)).subscribe((newState) => {
    //   this.updateState(newState);
    //   this.updateAll();
    // });
    this.positionController.updateState$.pipe(takeUntil(this.ngUnsubscribe)).subscribe((newState) => {
      this.updateState(newState);
      this.updateAll();
      this.updateElementControls();
    });

    this.positionController.commitState$.pipe(takeUntil(this.ngUnsubscribe)).subscribe((newState) => {
      this.updateState(newState);
      this.positionController.activeState = cloneDeep(this.currentState);
      this.positionController.nextState = cloneDeep(this.currentState);
      this.updateAll();
      this.updateElementControls();
    });

    this.positionController.commitLayout$.pipe(takeUntil(this.ngUnsubscribe)).subscribe((newState) => {
      this.layout = this.positionController.toLayout();
      this.positionController.layout = cloneDeep(this.layout);
    });

    this.cd.detectChanges();
    this.initLayout();
  }

  updateState(newState: LayoutState) {
    this.previousState = this.currentState;
    this.currentState = newState;
  }
  initLayout() {
    this.positionController.initState({
      width: this.wrapperEl.nativeElement.clientWidth,
      height: this.wrapperEl.nativeElement.clientHeight,
    });
  }
  enableBuildMode() {
    Object.keys(this.layoutElements).forEach((id: string) => {
      this.enableBuildModeOnElement(id);
    });
    this.layoutControlsRef.instance.buildMode = true;
    this.cd.detectChanges();
  }
  enableBuildModeOnElement(id: string) {
    const el = this.layoutElements[id];
    this.renderer.addClass(el.componentRef.instance.pluginComponentRef.location.nativeElement, "limit-interaction");
    this.renderer.addClass(el.componentRef.location.nativeElement, "editing");
    el.componentRef.changeDetectorRef.detectChanges();

    el.listeners.keydown = this.renderer.listen(document, "keydown", (evt) => {
      if (evt.key === "Control") {
        this.activeElementEngaged = true;
      }
    });
    el.listeners.keyup = this.renderer.listen(document, "keyup", (evt) => {
      if (evt.key === "Control") {
        this.activeElementEngaged = false;
      }
    });

    el.listeners.click = this.renderer.listen(el.componentRef.location.nativeElement, "click", (evt) => {
      this.activate(id, evt.ctrlKey);
      if (evt.ctrlKey) {
        this.activeElementEngaged = true;
      }
    });

    el.listeners.mouseover = this.renderer.listen(el.componentRef.location.nativeElement, "mouseover", (evt) => {
      if ((this.activeElementIds.length <= 1 || !evt.ctrlKey) && !this.activeElementEngaged) {
        this.activate(id);
      } else {
        this.setLastHovered(id);
      }
    });
  }

  setLastHovered(id) {
    this.lastHoveredElement = id;
  }

  disableBuildMode() {
    if (this.layoutControlsRef?.instance.buildMode) {
      this.layoutControlsRef.instance.buildMode = false;
    }
    this.deactivate();
    Object.keys(this.layoutElements).forEach((key: string) => {
      const el = this.layoutElements[key];
      if (el.listeners.click) {
        el.listeners.click();
      }
      if (el.listeners.mouseover) {
        el.listeners.mouseover();
      }
      this.renderer.removeClass(el.componentRef.instance.pluginComponentRef.location.nativeElement, "limit-interaction");
      this.renderer.removeClass(el.componentRef.location.nativeElement, "editing");
    });
    this.cd.detectChanges();
  }
  activate(id, add = false) {
    this.setLastHovered(null);
    const el = this.layoutElements[id];
    this.renderer.addClass(el.componentRef.location.nativeElement, "editing-active");

    if (add) {
      this.activeElementIds.push(id);
    } else {
      this.deactivate();
      this.activeElementIds = [id];
    }
    if (!this.activeElementControls) {
      this.activeElementControls = this.createElementControls(this.activeElementIds, this.wrapperVC);
    }
    this.positionController.controlComponent = this.activeElementControls;
    this.activeElementControls.instance.setIds(this.activeElementIds);

    this.updateElementControls();
  }
  deactivate() {
    this.activeElementIds.forEach((id) => {
      const el = this.layoutElements[id];
      this.renderer.removeClass(el.componentRef.location.nativeElement, "editing-active");
      if (id === this.lastHoveredElement) {
        this.setLastHovered(null);
      }
    });
    this.activeElementEngaged = false;
    if (this.activeButtonControl) {
      this.activeButtonControl.dispose();
    }
    if (this.activeElementControls) {
      this.activeElementControls.destroy();
      this.activeElementControls = null;
    }
    this.activeElementIds = [];
    if (this.lastHoveredElement !== null) {
      this.activate(this.lastHoveredElement);
    }
  }
  subscribeToElementWrapperEvents(subject: Subject<any>, callback: any) {
    subject
      .pipe(
        takeUntil(this.editModeExited$),
        takeUntil(this.buildModeCancelled$),
        takeUntil(this.buildModeEnded$),
        takeUntil(this.ngUnsubscribe)
      )
      .subscribe(callback);
  }

  createLayoutControls() {
    const componentFactory = this.cfr.resolveComponentFactory(LayoutControlsComponent);
    const componentRef = this.hostVC.createComponent(componentFactory, undefined, this.injector);
    componentRef.instance.buildMode = this.buildMode$.value;

    this.subscribeToLayoutControlEvent(componentRef.instance.buildModeEntered, () => {
      this.buildMode$.next(true);
    });
    this.subscribeToLayoutControlEvent(componentRef.instance.buildModeEnded, () => {
      this.buildModeEnded$.next(this.positionController.toLayout());
      this.buildMode$.next(false);
    });
    this.subscribeToLayoutControlEvent(componentRef.instance.buildModeCancelled, () => {
      this.buildMode$.next(false);
      this.buildModeCancelled$.next();
    });

    componentRef.changeDetectorRef.detectChanges();
    this.layoutControlsRef = componentRef;
  }
  subscribeToLayoutControlEvent(subject: Subject<any>, callback: any) {
    subject.pipe(takeUntil(this.editModeExited$), takeUntil(this.ngUnsubscribe)).subscribe(callback);
  }
  removeLayoutControls() {
    if (this.layoutControlsRef) {
      this.layoutControlsRef.destroy();
    }
  }

  layoutConfigChange(newLayoutConfig: LayoutConfig) {
    const diff = getDiff(this.layoutConfig, newLayoutConfig);
    const diffs = deepCompare(this.layoutConfig, newLayoutConfig);

    if (diff.buildMode) {
      this.buildMode$.next(newLayoutConfig.buildMode);
    }
    if (diff.editMode) {
      this.editMode$.next(newLayoutConfig.editMode);
    }
    this.positionController.onLayoutConfigChange(newLayoutConfig, diffs);
    this.layoutConfig = newLayoutConfig;
  }
  elementsChange() {
    const collectedLayout = {};
    Object.keys(this.elements).forEach((key) => {
      const elLayout = this.elements[key].instance.pluginConfig?.layout;
      if (!_.isNil(elLayout) && elLayout.type === "absolute") {
        collectedLayout[key] = elLayout;
      }
    });

    const diffs = deepCompare(this.currentLayout, collectedLayout);
    const newEls = cloneDeep(diffs.new) as any;
    Object.keys(newEls).forEach((id) => {
      const initialState = this.initLayoutElement(id, collectedLayout[id]);
      if (this.buildMode$.value) {
        this.enableBuildModeOnElement(id);
      }
      newEls[id] = initialState;
    });

    Object.keys(diffs.deleted).forEach((id) => {
      if (this.activeElementIds.indexOf(id) > -1) {
        this.deactivate();
      }
      this.destroyLayoutElement(id);
    });

    this.currentLayout = collectedLayout;
    this.positionController.onLayoutChange(collectedLayout, newEls, diffs.deleted, diffs.updated);
  }

  initLayoutElements() {
    const initialState = {};
    Object.keys(this.layout).forEach((id: string) => {
      const config = this.layout[id] as LayoutElementConfig;
      if (config.type === "absolute") {
        initialState[id] = this.initLayoutElement(id, config);
      }
    });
    return initialState;
  }
  initLayoutElement(id: string, config: LayoutElementConfig) {
    this.layoutElements[id] = this.createLayoutElement(id, this.elements[id]);
    const wrapperInstance = this.layoutElements[id].componentRef.instance;
    this.renderer.setStyle(this.layoutElements[id].componentRef.location.nativeElement, "position", "absolute");
    // this.renderer.setStyle(this.layoutElements[key].componentRef.location.nativeElement, "transition", "left 0.2s, top 0.2s");

    if (config.initialMaxW !== undefined) {
      this.renderer.setStyle(wrapperInstance.pluginComponentRef.location.nativeElement, "max-width", config.initialMaxW + "px");
    }

    if (config.initialMaxH !== undefined) {
      this.renderer.setStyle(wrapperInstance.pluginComponentRef.location.nativeElement, "max-height", config.initialMaxH + "px");
    }

    const rect = wrapperInstance.pluginComponentRef.location.nativeElement.getBoundingClientRect();
    const state: LayoutElementState = {
      left: 0,
      top: 0,
      width: 0,
      height: 0,
      renderedContentWidth: rect.width,
      renderedContentHeight: rect.height,
      naturalWidth: rect.width,
      naturalHeight: rect.height,
      zIndex: null,
    };

    if (config.initialMaxW !== undefined) {
      this.renderer.removeStyle(wrapperInstance.pluginComponentRef.location.nativeElement, "max-width");
    }
    if (config.initialMaxH !== undefined) {
      this.renderer.removeStyle(wrapperInstance.pluginComponentRef.location.nativeElement, "max-height");
    }

    wrapperInstance.contentChanged$.pipe(takeUntil(wrapperInstance.destroy$)).subscribe(() => {
      const newRect = wrapperInstance.pluginComponentRef.location.nativeElement.getBoundingClientRect();
      this.positionController.onElementSizeChanged(id, { renderedContentWidth: newRect.width, renderedContentHeight: newRect.height });
    });
    return state;
  }

  createLayoutElement(id, componentRef: ComponentRef<PluginWrapperComponent>) {
    const layoutElement: LayoutElement = {
      componentRef: componentRef,
      listeners: {},
    };
    return layoutElement;
  }
  createElementControls(id, vcRef: ViewContainerRef) {
    const componentFactory = this.cfr.resolveComponentFactory(LayoutElementControlsComponent);
    const componentRef = vcRef.createComponent(componentFactory, undefined, this.injector);
    componentRef.instance.ids = id;
    componentRef.instance.wrapperEl = this.hostEl;
    componentRef.instance.elementButtons = this.layoutConfig.elementButtons;
    componentRef.changeDetectorRef.detectChanges();
    this.subscribeToElementControlEvent(componentRef.instance.dragStart$, (e) => {
      this.activeElementEngaged = true;
      this.positionController.onDragStart(e.ids.length > 1 ? e.ids : e.ids[0], e.data, e.elementButton);
    });
    this.subscribeToElementControlEvent(componentRef.instance.drag$, (e) => {
      this.positionController.onDrag(e.ids.length > 1 ? e.ids : e.ids[0], e.data, e.elementButton);
    });
    this.subscribeToElementControlEvent(componentRef.instance.dragEnd$, (e) => {
      this.positionController.onDragEnd(e.ids.length > 1 ? e.ids : e.ids[0], e.data, e.elementButton);
    });

    this.subscribeToElementControlEvent(componentRef.instance.scaleStart$, (e) => {
      this.activeElementEngaged = true;
      this.positionController.onScaleStart(e.ids.length > 1 ? e.ids : e.ids[0], e.data);
    });
    this.subscribeToElementControlEvent(componentRef.instance.scale$, (e) => {
      this.positionController.onScale(e.ids.length > 1 ? e.ids : e.ids[0], e.data);
    });
    this.subscribeToElementControlEvent(componentRef.instance.scaleEnd$, (e) => {
      this.positionController.onScaleEnd(e.ids.length > 1 ? e.ids : e.ids[0], e.data);
    });

    this.subscribeToElementControlEvent(componentRef.instance.buttonEvent$, (e) => {
      this.positionController.onButtonEvent(e.ids.length > 1 ? e.ids : e.ids[0], e.data, e.elementButton);
      if (e.elementButton?.actions) {
        this.context.fireActions(e.ids.length > 1 ? e.ids : e.ids[0], e.elementButton.actions, null, null, e.elementButton).subscribe();
      }
    });
    this.subscribeToElementControlEvent(componentRef.instance.click$, (e) => {
      this.activeElementEngaged = true;
    });
    this.subscribeToElementControlEvent(componentRef.instance.mouseOver$, (e) => {
      // if (this.activeButtonControl) {
      //   this.activeButtonControl.dispose();
      // }
      // this.activeButtonControl = this.createControlButtons(this.elements[id].location);
    });
    this.subscribeToElementControlEvent(componentRef.instance.doubleClick$, (data) => {
      this.positionController.onDoubleClick(data.ids.length > 1 ? data.ids : data.ids[0], null);
      // this.updateControlButtons();
    });
    this.subscribeToElementControlEvent(componentRef.instance.mouseLeave$, (e) => {
      // if (this.activeButtonControl) {
      //   this.activeButtonControl.dispose();
      // }
    });
    return componentRef;
  }
  updateElementControls() {
    if (this.activeElementControls) {
      const state = this.combineStates(this.activeElementIds);
      this.renderer.setStyle(this.activeElementControls.location.nativeElement, "left", state.left + "px");
      this.renderer.setStyle(this.activeElementControls.location.nativeElement, "top", state.top + "px");
      this.renderer.setStyle(this.activeElementControls.location.nativeElement, "width", state.width + "px");
      this.renderer.setStyle(this.activeElementControls.location.nativeElement, "height", state.height + "px");
      this.renderer.setStyle(this.activeElementControls.location.nativeElement, "max-width", state.width + "px");
      this.renderer.setStyle(this.activeElementControls.location.nativeElement, "max-height", state.height + "px");
      this.renderer.setStyle(this.activeElementControls.location.nativeElement, "z-index", state.zIndex + 1000);
      this.activeElementControls.instance.update(state.left, state.top, state.width, state.height);
    }
    // this.updateControlButtons();
  }
  combineStates(ids: string[]) {
    const combinedState = cloneDeep(this.currentState[ids[0]]);
    let minX;
    let minY;
    let maxX;
    let maxY;
    for (let i = 0; i < ids.length; i++) {
      const id = ids[i];
      const state = this.currentState[id];
      if (minX === undefined || state.left < minX) {
        minX = state.left;
      }

      if (minY === undefined || state.top < minY) {
        minY = state.top;
      }

      if (maxX === undefined || maxX < state.left + state.width) {
        maxX = state.left + state.width;
      }

      if (maxY === undefined || maxY < state.top + state.height) {
        maxY = state.top + state.height;
      }
    }
    return { left: minX, top: minY, width: maxX - minX, height: maxY - minY, zIndex: combinedState.zIndex };
  }

  subscribeToElementControlEvent(subject: Subject<any>, callback: any) {
    subject
      .pipe(
        takeUntil(this.editModeExited$),
        takeUntil(this.buildModeCancelled$),
        takeUntil(this.buildModeEnded$),
        takeUntil(this.ngUnsubscribe)
      )
      .subscribe(callback);
  }

  onResize() {
    this.viewportW = this.hostEl.nativeElement.clientWidth;
    this.viewportH = this.hostEl.nativeElement.clientHeight;
    this.positionController.onResize({
      width: this.viewportW,
      height: this.viewportH,
    });
  }

  // Updating positions
  updateAll() {
    Object.keys(this.layoutElements).forEach((key) => {
      const el = this.layoutElements[key];
      this.updateStyle(key);
      // if (this.buildMode$.value) {
      //   el.controlsRef.instance.info = this.positionController.infoHook(key);
      // }
      el.componentRef.instance.redraw$.next();
    });
  }

  updateConfig(id: string, config: { x?: number; y?: number; w?: number; h?: number }) {
    const elConfig = this.currentLayout[id];
    Object.assign(elConfig, config);
  }

  // Applying css style to the element
  updateStyle(id: string) {
    const el = this.layoutElements[id];

    if (!this.currentState) {
      console.warn("State not yet inited");
      return;
    }
    const state = this.currentState[id];
    if (!state) {
      console.warn("State not defined for", id, el);
      return;
    }

    // this.renderer.setStyle(el.componentRef.location.nativeElement, "overflow", "hidden");
    this.renderer.setStyle(el.componentRef.location.nativeElement, "left", state.left + "px");
    this.renderer.setStyle(el.componentRef.location.nativeElement, "top", state.top + "px");
    this.renderer.setStyle(el.componentRef.location.nativeElement, "width", state.width + "px");
    this.renderer.setStyle(el.componentRef.location.nativeElement, "height", state.height + "px");
    this.renderer.setStyle(el.componentRef.location.nativeElement, "max-width", state.width + "px");
    this.renderer.setStyle(el.componentRef.location.nativeElement, "max-height", state.height + "px");
    this.renderer.setStyle(el.componentRef.location.nativeElement, "z-index", state.zIndex);
  }

  destroyLayoutElement(id: string) {
    if (this.layoutElements[id].listeners) {
      Object.keys(this.layoutElements[id].listeners).forEach((lkey) => {
        this.layoutElements[id].listeners[lkey]();
      });
    }
    delete this.layoutElements[id];
  }

  destroy() {
    this.disableBuildMode();
    // Cleaning up all listeners
    Object.keys(this.layoutElements).forEach((id) => {
      this.destroyLayoutElement(id);
    });
    if (this.resizeListener) {
      this.resizeListener();
    }
    if (this.backgroundClickListener) {
      this.backgroundClickListener();
    }
    if (this.backgroundMouseOverListener) {
      this.backgroundMouseOverListener();
    }
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }
}
