import { Subject } from "rxjs";
import { PluginWrapperComponent } from "../plugin-wrapper.component";
import { PluginInteractionConfig } from "../../interfaces";
import { takeUntil, finalize, debounceTime, tap } from "rxjs/operators";

export class InteractionBase {
  listeners: { el: HTMLElement; evt: any; boundFunc: any }[] = [];
  context: PluginWrapperComponent;
  destroy$: Subject<any> = new Subject();
  interaction: PluginInteractionConfig;
  id: string;
  pluginInstance: any;

  // dataHook = (data) => {
  //   return data;
  // };

  fire$ = new Subject();
  debouncedFire$ = new Subject();
  unsubscribe$ = new Subject();
  // requests;

  onCreate(id: string, interaction: PluginInteractionConfig, ...args: any[]) {}
  create(id: string, context: PluginWrapperComponent, interaction: PluginInteractionConfig, ...args: any[]) {
    this.id;
    this.context = context;
    this.interaction = interaction;
    this.fire$.pipe(takeUntil(this.destroy$), debounceTime(interaction.debounce)).subscribe((data) => {
      this.debouncedFire(data);
    });
    this.onCreate(id, interaction, ...args);
  }

  onUpdate() {}
  update() {
    this.onUpdate();
  }

  registerListener(el: HTMLElement, evt: string, boundFunc) {
    el.addEventListener(evt, boundFunc.bind(this));
    this.listeners.push({ el, evt, boundFunc });
  }

  onDestroy() {}
  destroy() {
    this.onDestroy();
    for (const listener of this.listeners) {
      listener.el.removeEventListener(listener.evt, listener.boundFunc);
    }
    this.destroy$.next();
    this.destroy$.complete();
  }

  fire(data: any = null) {
    // setTimeout((params) => {
    //   this.fire$.next(data);
    // });

    // return this.debouncedFire$.pipe(
    //   tap((params) => {
    //     console.log("NEEXT");
    //   }),
    //   finalize(() => {
    //     this.unsubscribe$.next();
    //   })
    // );
    return this.context.fireActions(this.id, this.interaction.actions, null, this.interaction, data);
  }

  debouncedFire(data: any = null) {
    // this.context
    //   .fireActions(this.id, this.interaction.actions, null, this.interaction, data)
    //   .pipe(takeUntil(this.unsubscribe$))
    //   .subscribe((result) => {
    //     this.debouncedFire$.next(result);
    //   });
  }
}
