import { has, cloneDeep } from "lodash";
import { LayoutConfig, LayoutElementConfig, Layout } from "./group.interface";
import { PluginConfig } from "shared";

export const sortPopupsToEnd = (elements) => {
  return Object.keys(elements).sort((a, b) => {
    if (has(elements[a], "config.popup") && !has(elements[b], "config.popup")) {
      return 1;
    }
    if (!has(elements[a], "config.popup") && has(elements[b], "config.popup")) {
      return -1;
    }
    return 0;
  });
};

export const filterLayoutElementsByType = (layout: Layout, type) => {
  const filtered = {};
  Object.keys(layout).forEach((key: string) => {
    const el: LayoutElementConfig = layout[key];
    if (el.type === type) {
      filtered[key] = el;
    }
  });
  return filtered;
};

