export const sortElementKeysByLayout = (elements) => {
  const sortedElementKeys = Object.keys(elements).sort((a, b) => {
    const elA = elements[a] as PluginConfig;
    const elB = elements[b] as PluginConfig;
    const elALayoutConf: LayoutElementConfig = elA.layout;
    const elBLayoutConf: LayoutElementConfig = elB.layout;

    if (elALayoutConf && elBLayoutConf) {
      if (elALayoutConf.index !== undefined && elBLayoutConf.index !== undefined) {
        if (elALayoutConf.index < elBLayoutConf.index) {
          return -1;
        }
        if (elALayoutConf.index > elBLayoutConf.index) {
          return 1;
        }
      }

      if (elALayoutConf.index !== undefined) {
        return -1;
      }
      if (elBLayoutConf.index !== undefined) {
        return 1;
      }

      if (elALayoutConf.y < elBLayoutConf.y) {
        return -1;
      }
      if (elALayoutConf.y > elBLayoutConf.y) {
        return 1;
      }
      if (elALayoutConf.x < elBLayoutConf.x) {
        return -1;
      }
      if (elALayoutConf.x > elBLayoutConf.x) {
        return 1;
      }
      return 0;
    }
    if (!elALayoutConf) {
      return 1;
    }
    if (!elBLayoutConf) {
      return -1;
    }

    return 0;
  });
  return sortedElementKeys;
};

export const addStyles = (renderer, elem, style) => {
  if (style) {
    Object.keys(style).forEach((prop) => {
      renderer.setStyle(elem, prop, style[prop]);
    });
  }
};
