import { ElementButton } from "./../../../group.interface";
import { Component, OnInit, Input, Output, EventEmitter, ChangeDetectorRef } from "@angular/core";
import { Subject } from "rxjs";
import { processButtons } from "./utils";

@Component({
  selector: "app-layout-element-button-controls",
  templateUrl: "./layout-element-button-controls.component.html",
  styleUrls: ["./layout-element-button-controls.component.scss"],
})
export class LayoutElementButtonControlsComponent implements OnInit {
  _elementButtons: { [id: string]: ElementButton };

  @Input() set elementButtons(val: { [id: string]: ElementButton }) {
    this._elementButtons = val;
    this.processedButtons = processButtons(val);
  }
  get elementButtons(): { [id: string]: ElementButton } {
    return this._elementButtons;
  }

  @Output() buttonEvent: EventEmitter<{ event: MouseEvent; elementButton: ElementButton }> = new EventEmitter();

  processedButtons: ElementButton[];

  constructor(private cd: ChangeDetectorRef) {}

  ngOnInit(): void {}

  onButtonEvent(e: { event: MouseEvent; elementButton: ElementButton }) {
    this.buttonEvent.emit(e);
  }
}
